      var elem = this.$refs[id]
      if (!elem) return
      var rect = elem.getBoundingClientRect()
      var win = elem.ownerDocument.defaultView

      var offset2 = -10
      if (id === 'landingSaibaMais' && typeof window.orientation === 'undefined') {
        offset2 = -420
      }

      window.scrollTo({
        top: rect.top + win.pageYOffset + offset2,
        left: 0,
        behavior: 'smooth'
      });
      this.instrumentState.landingScroll = null
      
    }
  },
