<template>
  <div>
    <!-- Main container -->
    <br>
    <nav class="level">
      <!-- Left side -->
      <div class="level-left">
        <div class="level-item">
          <p class="subtitle is-5" v-if="!loadingInstrumentosFiltro">
            <strong v-if="instrumentosFiltro.length">{{instrumentosFiltro.length}}</strong>&nbsp;
            <span v-if="instrumentosFiltro.length > 1">instrumentos</span>
            <span v-if="instrumentosFiltro.length === 1">instrumento</span>
          </p>
          <a class="button is-text is-loading" v-if="loadingInstrumentosFiltro">
            
          </a>

        </div>
        <div class="level-item">
          <div class="field">

            <div class="control has-icons-left">
              <input v-model="termoBusca" class="input is-rounded" v-bind:class="{'is-loading': loadingInstrumentosFiltro}" type="text" placeholder="Buscar instrumento">

              <span class="icon is-medium is-left">
                <i class="fas fa-search"></i>
              </span>
            </div>

          </div>
        </div>

      </div>

      <!-- Right side -->
      <div class="level-right">
        <div class="tabs is-centered filtro-instrumentos-tabs" style="margin-top: 6px;">
          <ul>
            <li v-bind:class="{'is-active': filtroSituacao==='TODOS'}">
              <a @click="filtroSituacao='TODOS'">Todos</a>
            </li>
            <li v-bind:class="{'is-active': filtroSituacao==='CALIBRADO'}">
              <a @click="filtroSituacao='CALIBRADO'">Calibrados</a>
            </li>
            <li v-bind:class="{'is-active': filtroSituacao==='ALERTA_VENCIMENTO'}">
              <a @click="filtroSituacao='ALERTA_VENCIMENTO'">
                <span class="icon is-small has-text-warning"><i class="fas fa-exclamation-circle" aria-hidden="true"></i></span>
                <span>Alerta</span>
              </a>
            </li>
            <li v-bind:class="{'is-active': filtroSituacao==='VENCIDO'}">
              <a @click="filtroSituacao='VENCIDO'">
                <span class="icon is-small has-text-danger"><i class="fas fa-exclamation-circle" aria-hidden="true"></i></span>
                <span>Alerta</span>                  
              </a>
            </li>
            <li v-bind:class="{'is-active': filtroSituacao==='INATIVO'}">
              <a @click="filtroSituacao='INATIVO'">Inativos</a>
            </li>
          </ul>
        </div>
      </div>

    </nav>

    <div class="columns">
      <div class="column">
        <div>
          <p class="subtitle is-5" v-if="!loadingInstrumentosFiltro && instrumentosFiltro.length === 0">
            <span>Nenhum instrumento encontrado</span>
          </p>
          <article class="media" v-for="inst in instrumentosFiltro">
            <router-link :to="{ path: 'instrumento/'+inst.id+'/certificado'}" class="has-text-black">

              <div class="media-content">
                <div class="content">
                  <div>
                    {{inst.descricao}}
                  </div>
                  
                  <span class="tag" v-bind:class="[inst.status.cor]">{{inst.status.textoCap}}</span>&nbsp;

                  <span v-if="inst.statusChecagem" class="tag" v-bind:class="[inst.statusChecagem.cor]">{{inst.statusChecagem.texto}}</span>
                </div>
              </div>
            </router-link>
          </article>
        </div>

      </div>
    </div>

  </div>
  
</template>

<script>

import PouchDB from 'pouchdb'
import pouchAuth from 'pouchdb-authentication'
import Fuse from 'fuse.js'
PouchDB.plugin(pouchAuth);

  
export default {
  name: 'Instrumentos',
  props: [],
  data () {
    return {
      instrumentState: INSTRUMENT_STORE.state,
      termoBusca: '',
      filtroSituacao: 'TODOS'
    }
  },
  created () {
  },
  computed: {
    loadingInstrumentosFiltro () {
      return this.instrumentos.length === 0
    },
    instrumentos () {
      var res = Object.keys(this.instrumentState.instrumentos).map((instKey) => {
        return this.instrumentState.instrumentos[instKey]
      })
      var resSorted = _.sortBy(res, (item) => item.descricao)
      return resSorted
    },
    instrumentosFiltro () {

      var instrumentosFiltro
      if (this.filtroSituacao === 'TODOS') {
        instrumentosFiltro = this.instrumentos
      } else {
        instrumentosFiltro = this.instrumentos.filter((inst) => {
          return (inst.status.situacao === this.filtroSituacao) || (inst.statusChecagem && inst.statusChecagem.situacao === this.filtroSituacao)
        })
      }

      if (!this.termoBusca.length) {
        return instrumentosFiltro
      }

      var options = {
        shouldSort: true,
        threshold: 0.3,
        location: 0,
        distance: 500,
        maxPatternLength: 32,
        minMatchCharLength: 1,
        keys: [
          'item.descricao',
          'item.fabricante',
          'item.modelo',
          'item.identificacao',
          'item.numeroserie',
          'item.patrimonio'
        ]
      };

      var fuse = new Fuse(instrumentosFiltro, options)
      var res = fuse.search(this.termoBusca);

      /*var res = instrumentosFiltro.filter((inst) => {
        return inst.descricao.indexOf(this.termoBusca) !== -1
      })*/
      return res.map((x) => x.item)
    }
  }
}
</script>

