<template>
  <span>
    <div id="app">
      
      <app-nav v-if="instrumentState.db || instrumentState.isLanding"></app-nav>

      <landing v-if="!loading && !instrumentState.db && instrumentState.isLanding"></landing>
      <div class="container">
        <a v-if="loading" class="button is-loading is-text is-large is-fullwidth" style="font-size: 3em;"></a>
        <login v-if="!loading && !instrumentState.db && !instrumentState.isLanding" v-on:check_login="checkLogin"></login>
        <router-view v-if="!loading && instrumentState.db"></router-view>
      </div>


    </div>
    <div id="pdf-container"></div>
    <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.6.3/css/all.css" integrity="sha384-UHRtZLI+pbxtHCWp1t77Bi1L4ZtiqrqD80Kn4Z8NTSRyMA2Fd33n5dQ8lWUE00s/" crossorigin="anonymous">
    <link href="https://fonts.googleapis.com/css?family=Roboto" rel="stylesheet" type="text/css"/>
  </span>
</template>
<script>
import AppNav from '@/components/AppNav.vue'
import Landing from '@/components/Landing.vue'
import Login from '@/components/Login.vue'
import Instrumentos from '@/components/Instrumentos.vue'
import _ from 'lodash'
import {fnSignformatter,
fnHideOnClickOutside,
fnRegreArray,
fnPegaDataUTCNum,
fnTextoEntreTags,
fnFmtData,
fnCampoValidoParaExibicao,
fnDescItem,
fnPegaStatus,
fnPegaStatusChecagem,
fnQueryInstIdData,
fnConfigPeriodicidade} from './fn'

window.T = {
  'global': 'Análise Global',
  'fatorMultiplicativo': 'Fator',
  'aplicarCorrecao': 'Corrigir',
  'lhs': 'EMA',
  'calculoEscolha': 'Cálculo',
  'periodicidade': 'Periodicidade',
  'tolVal': 'Valor da Tolerância relativa',
  'tolUnidade': 'Unidade da Tolerância relativa',
  'tolValNaUnidade': 'Valor da Tolerância na Unidade',
  'true': 'Verdadeiro',
  'false': 'Falso',
  'tol': 'Personalizado',
  'ema': 'Certificado de Calibração'
}

window.addEventListener('online',function(){INSTRUMENT_STORE.state.online = true;console.log('online')})
window.addEventListener('offline',function(){INSTRUMENT_STORE.state.online = false})



import {Analises} from './analises'

import {INSTRUMENT_STORE} from './instrumentStore'
window.INSTRUMENT_STORE = INSTRUMENT_STORE
const state = INSTRUMENT_STORE.state

import axios from 'axios'
import PouchDB from 'pouchdb'
import pouchAuth from 'pouchdb-authentication'
PouchDB.plugin(pouchAuth);


var complementoPageView = function () {
  if (INSTRUMENT_STORE.state.userCtx) {
    return ' - ' + INSTRUMENT_STORE.state.userCtx.name
  }
  return ''
}


export default {
  name: 'App',
  components: {AppNav, Landing, Login, Instrumentos},
  watch: {
    'instrumentState.descItem': function() {
      document.title = 'ProCal - ' + this.instrumentState.descItem + complementoPageView()
    },
    '$route.query.modal': function(val) {
      if (!val) {
        Object.keys(this.instrumentState.modals).map((k) => {
          this.instrumentState.modals[k] = false
        })
        Object.assign(document.body.style, {
            marginRight: '0px'
        });
      } else {
        var elements = document.getElementsByClassName('modal-background')
        for (var i = 0; i < elements.length; i++) {
          elements[i].onclick = () => {
            Object.keys(this.instrumentState.modals).map((k) => {
              this.instrumentState.modals[k] = false
            })
          }
        }
      }

    },
    'instrumentState.modals': {
      handler: function (val) {
        
        var q = _.clone(this.$route.query)
        
        let marginRightPx = 0;
        if(window.getComputedStyle) {
            let bodyStyle = window.getComputedStyle(document.body);
            if(bodyStyle) {
                marginRightPx = parseInt(bodyStyle.marginRight, 10);
            }
        }

        let scrollbarWidthPx = window.innerWidth - document.body.clientWidth;
        Object.assign(document.body.style, {
            overflow: 'hidden',
            marginRight: `${marginRightPx + scrollbarWidthPx}px`
        });

        if (this.temModalAberto) {
          document.body.parentElement.style.overflow = 'hidden'
          q.modal = this.temModalAberto
        } else {
          document.body.parentElement.style.overflow = ''
          delete q.modal
          Object.assign(document.body.style, {
              overflow: '',
              marginRight: ''
          });

        }

        
        this.$router.push({query: q}).catch(err => {})
      },
      deep: true
    }
  },
  data () {
    return {
      loading: true,
      dbAddr: '',
      syncHandler: undefined,
      remoteDB: undefined,
      localDB: undefined,
      nomedb: undefined,
      sincronizando: null,
      instrumentState: INSTRUMENT_STORE.state,
      logged: null,
      timeoutState: {}
    }
  },
  created () {
    //console.log('check login')
    var client = axios({
      method: 'get',
      url: window.DB_ADDR
    })
    .then((response) => {
      //console.log(response)
    })
    .catch((error) => {
      console.log('definindo configuracao alternativa.')
      window.DB_ADDR = 'https://procal.ipt.br/_couch/'
    })
    .finally(() => {
      this.checkLogin()
    })
    
  },
  mounted () {
  },
  methods: {
    checkLogin () {
      // Checa login
      var loginDB = new PouchDB(window.DB_ADDR + '_users', {skip_setup: true, ajax: {timeout: 5000}})
      
      loginDB.getSession( (err, response) => {

        var userCtx
        if (err || !response.userCtx.name) {
          // offline ou nobody's logged in
          this.nomedb = window.localStorage.getItem('nomedb')
          try {
            userCtx = JSON.parse(window.localStorage.getItem('userCtx'))
            if (this.nomedb && userCtx) {
              this.logged = true
            }
          } catch (err2) {
            console.log(err2)
          }
        } else {
          window.localStorage.setItem('userCtx', JSON.stringify(response.userCtx))
          userCtx = response.userCtx
          this.logged = true
          // TODO: Verificar em todas as roles e de forma mais robusta
          const sep = '__'
          var re = /(__{1})(?!.*__)/g;
          var _array = re.exec(userCtx.roles[0])
          const indiceSep = re.lastIndex - sep.length

          this.nomedb = userCtx.roles[0].substring(0, indiceSep)
          window.localStorage.setItem('nomedb', this.nomedb)
        }
        //this.remoteDB = new PouchDB(window.DB_ADDR + this.nomedb, {skip_setup: true, ajax: {timeout: 5000}})

        this.remoteDB = new PouchDB(window.DB_ADDR + this.nomedb, {
          skip_setup: true, ajax: {timeout: 5000},
          fetch(url, opts) {
            opts.credentials='include'
            return PouchDB.fetch(url, opts)
          }
        }).on('error', (err) => {
          console.log(err)
          //debugger;
        });

        INSTRUMENT_STORE.remoteDB = this.remoteDB

        if (!userCtx || !this.nomedb) {
          // sem usuario registrado previamente 
          this.loading = false
          //if (!this.logged && !this.$route.query.token) {
          //}
          if (this.$route.query.action === 'login') {
            this.instrumentState.isLanding = false
          } else {
            this.instrumentState.isLanding = true
          }
          return
        }
        
        INSTRUMENT_STORE.state.userCtx = userCtx
        INSTRUMENT_STORE.state.usuarioTemAdminRole = userCtx.roles[0].indexOf('__admin') !== -1
        INSTRUMENT_STORE.state.usuarioGerencia = (['cli-demo__admin'].indexOf(userCtx.roles[0]) !== -1) ||
          (userCtx.name.indexOf('@ipt.br') !== -1) ||
          (userCtx.roles[0] === this.nomedb+'__admin')
        ;

        // Problemas com iOS e pouchdb. para estes casos, usar apenas db remoto
        // e caso nao esteja conectaco, redirecionar para login
        var iOS = /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream
        //if (iOS) {
        /*
        if (false) {
          console.log('iOS')
          if (!this.logged) {
            this.loading = false
            return
          }
          INSTRUMENT_STORE.state.db = this.remoteDB
        } else {
          //console.log(this.nomedb)
          this.localDB = new PouchDB(this.nomedb, {skip_setup: true, adapter: 'idb', auto_compaction: true})
          INSTRUMENT_STORE.state.db = this.localDB
        }
        */
        this.localDB = new PouchDB(this.nomedb, {skip_setup: true, adapter: 'idb', auto_compaction: true}).on('error', (err) => {
          console.log(err)
          //debugger;
        });
        
        // solucionando lentidao do map/reduce https://pouchdb.com/guides/queries.html
        /*
        this.loading = true
        this.localDB.query('analise/analise', {
          limit: 0 // don't return any results
        }).then(function (res) {
          console.log('index was built.')
        }).catch(function (err) {
          //
        }).finally(() => {
          this.loading = false
        });*/

        INSTRUMENT_STORE.state.db = this.localDB

        
        if (this.localDB && this.remoteDB && (this.localDB.adapter !== this.remoteDB.adapter)) {
          window.setTimeout(() => {
            this.setSync()
          }, 1000)
        }

        if (this.logged) {
          this.getAnaliseFn();
          this.queryInstrumentosData()
          .then(() => {
            if (!this.$route.params.id) {
              this.$router.push({ path: '/instrumentos' }).catch(err => {})
              document.title = 'ProCal' + complementoPageView()
            } else {
              //console.log(this.$route)
            }
            this.loading = false
          })
        }
      })
    },
    getAnaliseFn (db) {
      INSTRUMENT_STORE.state.Analises = Analises;

      
      // inspeciona objeto para obter funções disponíveis
      var analises = new INSTRUMENT_STORE.state.Analises({config: []}, true);
      var codCli = INSTRUMENT_STORE.state.userCtx.roles[0].split('__')[0];

      INSTRUMENT_STORE.state.funcoes = JSON.parse(JSON.stringify(analises.funcoes));

      if (codCli === 'cli-508') { // LME mantem todas as analises existentes
        INSTRUMENT_STORE.state.funcoes = JSON.parse(JSON.stringify(analises.funcoes));
      } else if (codCli === 'cli-95') { // Volks 
        INSTRUMENT_STORE.state.funcoes = JSON.parse(JSON.stringify(analises.funcoes));
        delete INSTRUMENT_STORE.state.funcoes['somaQuadUEMAMaisE'];
      } else { // por padrão, apenas Certificado de calibração e personalizado
        delete INSTRUMENT_STORE.state.funcoes['somaQuadUEMAMaisE'];
        delete INSTRUMENT_STORE.state.funcoes['AvaliacaoVolks'];
      }
      
    },
    queryInstrumentosData (db) {
      var targetdb = db || INSTRUMENT_STORE.state.db
      return targetdb.query('instrumentos/lista').then( (data) => {
        var docsConfig = data.rows.filter((item) => item.id.indexOf('_config_') !== -1)
        var docsChecagem = data.rows.filter((item) => item.value.dataDaChecagem && item.value.dataDaProximaChecagem)
        var docsInst = data.rows.filter((item) => !!item.value.dataUltimaCal)
        var instrumentos = {}
        docsInst.map((inst) => {
          var configPeriodicidadeResArr = docsConfig
            .filter((conf) => conf.key[0] === inst.id)
            .map((conf) => {return conf.value})
          ;

          var configPeriodicidade = fnConfigPeriodicidade(
            configPeriodicidadeResArr, inst.value.AlPeriodoCalibracao, inst.value.AlTipoPeriodo
          )

          var status = fnPegaStatus(inst.value.dataUltimaCal, configPeriodicidade)

          var docsChecagemResArr = docsChecagem
            .filter((conf) => conf.key[0] === inst.id)
            .map((conf) => {return conf.value})
          ;
          var statusChecagem = fnPegaStatusChecagem(docsChecagemResArr)
          
          this.$set(instrumentos, inst.id, {
            status: status,
            statusChecagem: statusChecagem,
            descricao: fnDescItem(inst.value),
            item: inst.value,
            id: inst.id
          })
          
          /*instrumentos[inst.id] = {
            status: status,
            descricao: fnDescItem(inst.value),
            item: inst.value,
            id: inst.id
          }*/
        })
        INSTRUMENT_STORE.updateInstrumentos(instrumentos)
              
      }).catch( (err) => {
        // some error
        if (err.reason === 'missing') {
          return this.queryInstrumentosData(this.remoteDB)
        } else {
          console.log(err)
        }
      });
    },
    queryInstIdData: fnQueryInstIdData,
    setSync () {
      var syncHandler = PouchDB.sync(this.localDB, this.remoteDB, {
        live: true,
        retry: true,
        filter: 'suprimeAnexos/suprimeAnexos'
      }).on('change', (data) => {
        var change = data.change
        if (change.docs.length) {
          //this.queryInstrumentosData()
          // Debounce a consulta da lista dos instrumentos
          var listaTimeoutId = this.timeoutState['_lista']
          if (listaTimeoutId) {
            clearTimeout(listaTimeoutId)
          }
          this.timeoutState['_lista'] = setTimeout(() => {
            this.queryInstrumentosData()
          }, 500)

          
          for (var i = 0; i < change.docs.length; i++) {

            if (('config_'+this.$route.params.id === change.docs[i].type) || ('ocorr_'+this.$route.params.id === change.docs[i].type) || change.docs[i]._id === this.$route.params.id) {
              // Debounce a consulta
              var timeoutId = this.timeoutState[this.$route.params.id]
              if (timeoutId) {
                clearTimeout(timeoutId)
              }
              this.timeoutState[this.$route.params.id] = setTimeout(() => {
                this.queryInstIdData()
              }, 500)
            }
          }
          
        }
      }).on('paused', (err) => {
        this.sincronizando = false
        console.log('sync paused')
        // replication paused (e.g. replication up to date, user went offline)
      }).on('active', () => {
        console.log('sync active')
        this.sincronizando = true
        // replicate resumed (e.g. new changes replicating, user went back online)
      }).on('denied', (err) => {
        console.log('denied', err)
        // a document failed to replicate (e.g. due to permissions)
      }).on('complete', (info) => {
        this.sincronizando = false
        console.log('sync complete')
        // handle complete
      }).on('error', (err) => {
        console.log('err', err)
        // handle error
      })
    }
  },
  computed: {
    temModalAberto () {
      var modals = this.instrumentState.modals
      return Object.keys(modals).reduce((acc, curr ) => {return acc || modals[curr]}, false)
    }
  }
}
</script>
