                  > <span class="label is-small">meses</span>
                </p>
                <br>
              <span class="icon">
                <i class="fas fa-info-circle"></i>
              </span>Todos os instrumentos tem como padrão a periodicidade de 1 ano com alerta 2 meses antes do vencimento.
              </div>
            </div>


          </div>

          <div class="">
            <a class="button is-light" @click="periodicidade.val = instrumentState.configPeriodicidade.periodicidade.val;instrumentState.modals.configDialog = false">
              Cancelar
            </a>
            <a class="button is-link" @click="handleConfirmConfig">
              Confirmar
            </a>
