        </div>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="periodicidade.val = instrumentState.configPeriodicidade.periodicidade.val;instrumentState.modals.configDialog = false"></button>
    </div>
    <!-- /Config -->

    <!-- #Remover Instrumento -->
    <div class="modal modal-fx-fadeInScale" v-bind:class="instrumentState.modals.confirmarRemoverInstrumento ? 'is-active' : ''">
      <div class="modal-background"></div>
      <div class="modal-content">
        <div class="box">
          <h6 class="title is-6">Atenção</h6>

          <p class="field">
            O Instrumento será removido. Tem certeza?
          </p>

          <div class="field is-grouped">
            <p class="control">
              <a class="button is-small" @click="instrumentState.modals.confirmarRemoverInstrumento = false">
                Cancelar
              </a>
            </p>
            <p class="control">
              <a class="button is-small is-link" :class="gravandoNovoInstrumento ? 'is-loading' : ''" @click="removerInstrumento">
                Confirmar
              </a>
