          </div>

        </div>
      </div>
    </div>
    <!-- /Remover Instrumento -->

    <!-- #Help Faixa -->
    <div class="modal modal-fx-fadeInScale" v-bind:class="instrumentState.modals.helpFaixa ? 'is-active' : ''">
      <div class="modal-background"></div>
      <div class="modal-content" style="width: 80%;">
        <div class="box">

          <article class="media">
            
            <div class="media-content">

              <h6 class="title is-6">Faixa</h6>

              <div class="help-modal-content">
                <span>
                  <p>Este gráfico exibe os erros de calibração com suas respectivas barras de incerteza, em função dos pontos calibrados, do certificado de calibração e das regras de decisão correspondentes à data selecionada na opção <strong>Dados até</strong>, para a faixa selecionada.</p>
                  <p>Através da opção <strong>Resposta em frequência</strong>, os dados podem ser visualizados em função da frequência, quando possível.</p>
                  <p>Neste gráfico são visualizadas correções, quando aplicadas através do módulo <strong>Regras de Decisão</strong>.</p>
                </span>
              </div>
            </div>
          </article>
        </div>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="instrumentState.modals.helpFaixa = false"></button>
    </div>
    <!-- /Help Faixa -->

    <!-- #Help Tendencia -->
    <div class="modal modal-fx-fadeInScale" v-bind:class="instrumentState.modals.helpTendencia ? 'is-active' : ''">
      <div class="modal-background"></div>
      <div class="modal-content" style="width: 80%;">
        <div class="box">

          <article class="media">
            
            <div class="media-content">

              <h6 class="title is-6">Tendência</h6>

              <div class="help-modal-content"><span><p>
              Este gráfico exibe os erros de calibração em função das datas de calibrações, para o ponto selecionado. A seleção do ponto é feita através do menu de seleção do ponto localizado logo acima do gráfico. <em>Neste gráfico não são visualizadas correções</em>,
              quando aplicadas através do módulo <strong>Regras de Decisão</strong>.
              </p></span></div>

            </div>
          </article>
        </div>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="instrumentState.modals.helpTendencia = false"></button>
    </div>
    <!-- /Help Tendencia -->

    <!-- #Help Regra -->
    <div class="modal modal-fx-fadeInScale" v-bind:class="instrumentState.modals.helpRegra ? 'is-active' : ''">
      <div class="modal-background"></div>
      <div class="modal-content" style="width: 80%;">
        <div class="box">

          <article class="media">
            
            <div class="media-content">

              <h6 class="title is-6">Regras de Decisão</h6>

                <div class="help-modal-content">
                  <p>Este módulo permite a aplicação de correções e análises aos resultados de uma calibração, através da definição dos seguintes parâmetros:
                  </p> 
                  <p>
                    <strong>Corrigir</strong>: é efetuada uma correção (Erro = 0) aos dados da calibração. As equações resultantes dessa correção são disponibilizadas no módulo <strong>Fórmulas e Aplicação</strong>. A interpolação entre os pontos calibrados é feita linearmente.
                  </p> 

                  <p>
                    <strong>Fator</strong>: é selecionado um número inteiro que multiplicará o valor resultante do parâmetro 
                    <strong>Cálculo</strong>.
                  </p> 
                  <p>
                    <strong>EMA</strong>: é selecionado o limite que será exibido no gráfico da faixa, que será o mesmo utilizado na comparação para a Regra de Decisão. As opções para <strong>EMA</strong> são:
                  </p> 
                  <ul>
                    <li>
                      <strong>Certificado de Calibração</strong>: Erro Máximo Admissível especificado pelo fabricante que consta no Certificado de Calibração correspondente.
                    </li> 
                    <li>
                      <strong>Personalizado</strong>: valor definido pelo usuário.
                    </li>
                  </ul> 
                  <p>
                    A conformidade é definida de acordo com a <strong>Regra de Decisão</strong> e com os parâmetros ajustados pelo usuário. Os resultados são exibidos no módulo <strong>Dados e Resultados</strong>.
                  </p>

                  <p>As configurações deste módulo podem ser gravadas para posterior consulta através do botão 
                    <strong>Gravar configurações</strong>. Ao selecionar a opção 
                    <strong>Aplicar à todas as faixas</strong>, as configurações serão automaticamente estendidas e gravadas para todas as faixas do instrumento. Todas as alterações de parâmetros são registradas e podem ser consultadas no menu principal estendido, na opção <strong>Ocorrências</strong>.
                  </p>

                </div>


            </div>
          </article>
        </div>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="instrumentState.modals.helpRegra = false"></button>
    </div>
    <!-- /Help Regra -->

    <!-- #Help Formulas -->
    <div class="modal modal-fx-fadeInScale" v-bind:class="instrumentState.modals.helpFormulas ? 'is-active' : ''">
      <div class="modal-background"></div>
      <div class="modal-content" style="width: 80%;">
        <div class="box">

          <article class="media">
            
            <div class="media-content">

              <h6 class="title is-6">Fórmulas e Aplicação</h6>

                <div class="help-modal-content"><span><p>Este módulo permite obter o valor correspondente à leitura do instrumento já com as configurações selecionadas no módulo <strong>Regras de Decisão</strong>. Para isso, basta digitar um valor no campo Indicação do instrumento, e a leitura com a análise e correção (quando habilitada) será mostrada logo abaixo.</p> <p>As equações resultantes das análises utilizadas, são disponibilizadas para o usuário. Essas equações estão formatadas para planilhas Excel, com possibilidade de seleção de célula de origem.</p></span></div>                

            </div>
          </article>
        </div>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="instrumentState.modals.helpFormulas = false"></button>
    </div>
    <!-- /Help Formulas -->

    <!-- #Help Dados -->
    <div class="modal modal-fx-fadeInScale" v-bind:class="instrumentState.modals.helpDados ? 'is-active' : ''">
      <div class="modal-background"></div>
      <div class="modal-content" style="width: 80%;">
        <div class="box">

          <article class="media">
            
            <div class="media-content">

              <h6 class="title is-6">Dados e Resultados</h6>

              <div class="help-modal-content">
                <p>Este módulo apresenta os resultados mais relevantes ao usuário, para cada ponto calibrado. Também são mostrados os resultados de conformidade, de acordo com os parâmetros definidos no módulo <strong>Regras de Decisão.</strong></p>
              </div>

            </div>
          </article>
        </div>
      </div>
      <button class="modal-close is-large" aria-label="close" @click="instrumentState.modals.helpDados = false"></button>
    </div>
    <!-- /Help Dados -->



  </nav>

</template>

<script>
import flatPickr from 'vue-flatpickr-component';
import pt from 'flatpickr/dist/l10n/pt.js';
import axios from 'axios'

flatpickr.localize(pt.pt);

import 'flatpickr/dist/flatpickr.css';
import moment from '../momentLocale'

import {fnSignformatter,
fnHideOnClickOutside,
fnRegreArray,
fnPegaDataUTCNum,
fnTextoEntreTags,
fnFmtData,
fnCampoValidoParaExibicao,
fnDescItem,
fnPegaStatus,
fnPegaStatusChecagem,
fnQueryInstIdData,
fnConfigPeriodicidade} from '../fn'


const DADOS_INST_TEMPLATE = {
  _id: '',
  descricao: null,
  modelo: null,
  fabricante: null,
  identificacao: null,
  numeroserie: null,
  patrimonio: null,
  AlTipoPeriodo: 3, // meses
  AlPeriodoCalibracao: 12,
  empresa: 'Instrumento criado pelo cliente',
  type: 'instrumentoCliente',
  calibracoes: [ {
      Certificado: null,
      CertificadoAl: null,
      HoraInicial: null,
      Faixas: undefined
    }]
}

export default {
  name: 'app-nav',
  props: [],
  components: {
    flatPickr
  },
  watch : {
    'instrumentState.configPeriodicidade': {
      handler: function() {
        this.periodicidade.val = this.instrumentState.configPeriodicidade.periodicidade.val
        this.periodicidade.status = this.instrumentState.configPeriodicidade.periodicidade.status || 'Ativo'
      },
      deep: true
    },
    dataDaChecagem (data) {this.configuraDataInputChecagem()},
    ocorrenciaDeChecagemIntermediaria (val) {
      if (val) {this.configuraDataInputChecagem()};
    },
    listaModelos () {
      return
      /*
      var list = this.$refs.inputModeloDataList
      this.listaModelos.forEach(function(item){
         var option = document.createElement('option');
         option.value = item;
         list.appendChild(option);
      })*/

      //this.$refs.inputModelo.list = this.$refs.inputModeloDataList
      //this.$refs.inputModelo.setAttribute('list', 'modelList')
    }
  },
  data () {
    return {
      instrumentState: INSTRUMENT_STORE.state,
      //instrumentState.configPeriodicidade.periodicidade: 
      burgerActive: false,
      enviandoMsg: false,

      /* #Gestao de usuarios */
      usersForm: {
        username: '',
        password: ''
      },

      gerenciandoUsuariosLogged: false,
      processandoAcaoUsuarios: false,
      usuarioPodeGerirUsuarios: false,
      userList: [],
      novoUsername: '',
      /* /Gestao de usuarios */

      /* #Config */
      periodicidade: {
        val: null,
        status: 'Ativo'
      },
      periodicidadeChecagem: null,
      alertaPeriodicidadeChecagem: false,
      //periodicidadeAnterior: {val: null}
      /* /Config */
      ocorrenciaDesc: '',
      categoriasSelecionadas: [],
      dataDaChecagem: null,
      dataDaProximaChecagem: null,

      gravandoOcorrencia: false,
      novaOcorrenciaFormVisivel: false,      
      formNovoInstrumento: JSON.parse(JSON.stringify(DADOS_INST_TEMPLATE)),
      gravandoNovoInstrumento: false
    }
  },
  mounted () {
  },
  methods: {
    configuraDataInputChecagem () {
      this.$nextTick(() => {
        // desabilita datas anteriores a data de checagem
        var dataDaChecagem = this.$refs.dataDaChecagemEl.fp.selectedDates[0];
        this.$refs.dataDaProximaChecagemEl.fp.set('disable', 
          [function(date) {
              return (date < dataDaChecagem);
          }]
        );

        if (this.$refs.dataDaProximaChecagemEl.fp.selectedDates[0] < dataDaChecagem) {
          this.$refs.dataDaProximaChecagemEl.fp.setDate(dataDaChecagem)
        }
      })
    },
    novaCalibracao () {
      this.formNovoInstrumento.calibracoes.push(JSON.parse(JSON.stringify(DADOS_INST_TEMPLATE.calibracoes[0])))
    },
    removeCalibracao (i) {
      this.formNovoInstrumento.calibracoes.splice(i, 1)
    },
    removerInstrumento () {
      this.instrumentState.db.get(this.instrumentState.data.inst._id).then((doc) => {
        doc._deleted = true;
        return this.instrumentState.db.put(doc);
      }).then((result) => {
        this.instrumentState.modals.confirmarRemoverInstrumento = false
        this.$router.push({ path: '/instrumentos' }).catch(err => {})
        this.$toasted.show('Instrumento Removido', {icon: 'fa-check-circle'})
      }).catch((err) => {
        console.log(err);
        this.$toasted.show('Erro ao remover Instrumento', {icon: 'fa-exclamation-triangle'})
      });
    },
    alterarInstrumento () {
      if (!this.emInstrumento) {
        this.formNovoInstrumento = JSON.parse(JSON.stringify(DADOS_INST_TEMPLATE))
        this.instrumentState.modals.novoInstrumento = true
        return
      }

      this.instrumentState.db.get(this.instrumentState.data.inst._id).then((doc) => {
        this.formNovoInstrumento = doc
        for (var i = 0; i < this.formNovoInstrumento.calibracoes.length; i++) {
          this.formNovoInstrumento.calibracoes[i].HoraInicial = moment(this.formNovoInstrumento.calibracoes[i].HoraInicial, moment.defaultFormat).format('DD/MM/YYYY')
        }
        this.instrumentState.modals.novoInstrumento = true
      }).catch((err) => {
        console.log(err);
        this.$toasted.show('Erro ao alterar o instrumento.', {icon: 'fa-exclamation-triangle'})
      });      
    },
    novoInstrumento () {
      if (!this.novoInstrumentoValido) return

      this.gravandoNovoInstrumento = true
      this.formNovoInstrumento._id = this.formNovoInstrumento._id || 'cli-'+~~(Date.now()/1000);

      var campos = ['modelo','fabricante','identificacao','numeroserie','patrimonio']
      campos.map((k) => {
        if (!this.formNovoInstrumento[k] || _.trim(this.formNovoInstrumento[k]).length === 0) {
          this.formNovoInstrumento[k] = ''
        }
      })

      for (var i = 0; i < this.formNovoInstrumento.calibracoes.length; i++) {
        this.formNovoInstrumento.calibracoes[i].HoraInicial = moment(this.formNovoInstrumento.calibracoes[i].HoraInicial, 'DD/MM/YYYY').format(moment.defaultFormat)
        this.formNovoInstrumento.calibracoes[i].CertificadoAl = this.formNovoInstrumento.calibracoes[i].Certificado
        this.formNovoInstrumento.calibracoes[i].Faixas = this.formNovoInstrumento.calibracoes[i].Faixas || []
      }

      this.formNovoInstrumento.calibracoes = _.reverse(_.sortBy(this.formNovoInstrumento.calibracoes, (item) => {
        return Date.UTC.apply(undefined, item.HoraInicial.split("T")[0].split("-"))
      }))

      this.instrumentState.db.put(this.formNovoInstrumento).then( (response) => {
        this.$toasted.show('Instrumento registrado.', {icon: 'fa-check-circle'})
        this.instrumentState.modals.novoInstrumento = false
        if (!this.emInstrumento) {
          this.$router.push({ path: '/instrumento/'+this.formNovoInstrumento._id }).catch(err => {})
        }
      }).catch( (err) => {
        console.log(err)
        this.$toasted.show('Não foi possível registrar o instrumento.', {icon: 'fa-exclamation-triangle'})
      }).finally( () => {
        this.gravandoNovoInstrumento = false
      })
    },
    novaOcorrencia () {
      this.gravandoOcorrencia = true

      var timestamp = Date.now()
      var ocorr = {
        _id: ''+this.instrumentState.data.inst._id+'_ocorr_'+timestamp,
        autor: this.instrumentState.userCtx['name'],
        timestamp: timestamp,
        desc: this.ocorrenciaDesc,
        categorias: this.categoriasSelecionadas
      }

      if (this.ocorrenciaDeChecagemIntermediaria) {
        ocorr.dataDaChecagem = moment(this.dataDaChecagem, 'DD/MM/YYYY').unix()*1000;
        ocorr.dataDaProximaChecagem = moment(this.dataDaProximaChecagem, 'DD/MM/YYYY').unix()*1000;
        ocorr.timestamp = ocorr.dataDaChecagem;
        ocorr.type = 'ocorr_'+this.instrumentState.data.inst._id;
      } else {
        ocorr.type = 'ocorr_'+this.instrumentState.data.inst._id;
      }

      this.instrumentState.db.put(ocorr).then( (response) => {
        this.$toasted.show('Ocorrência registrada.', {icon: 'fa-check-circle'})
        //limpa formulario de nova ocorrencia
        this.ocorrenciaDesc = ''
        this.categoriasSelecionadas = []
        this.novaOcorrenciaFormVisivel = false
      }).catch( (err) => {
        this.$toasted.show('Não foi possível registrar a ocorrencia.', {icon: 'fa-exclamation-triangle'})
      }).finally( () => {
        this.gravandoOcorrencia = false
      })

    },
    msgboxEnviaEmail () {
      this.enviandoMsg = true
      var msg = '<p>'+this.$refs.solicMsg.value+'\n' +'</p>'+
      '<p>email: '+ localStorage.getItem('ms_username') +'</p>'+
      '<p>nome: '+ (localStorage.getItem('displayName') || 'sem nome informado') +'</p>'+
      '<p>empresa: '+ (this.instrumentState.data.inst.empresa || 'sem nome definido')+'</p>'

      axios({
        method: 'post',
        url: 'https://procal.ipt.br/couch_user_manager/call_after_challenge',
        timeout: 5000,
        data: {
          data: msg,
          doc_id: this.instrumentState.data.inst._id,
          rev: this.instrumentState.data.inst._rev,
          db_name: window.localStorage.getItem('nomedb')
        }
      })
      .then((response) => {
        this.$toasted.show('Mensagem enviada.', {icon: 'fa-check-circle'} )
      })
      .catch((error) => {
        console.log(error)
        this.$toasted.show('Erro ao enviar mensagem.', {icon: 'fa-exclamation-triangle'} )
      })
      .finally(() => {
        this.enviandoMsg = false
      })
    },
    pegaSessaoGestaoUsuarios () {
      var client = axios({
        method: 'get',
        url: 'https://procal.ipt.br/couch_user_manager/session',
        withCredentials: true
      })
      .then((response) => {
        this.usuarioPodeGerirUsuarios = response.data
        if (response.data) {
          this.pegaListaUsuarios()
          this.gerenciandoUsuariosLogged = true
        } else {

        }
        
      })
      .catch((error) => {
        console.log(error)
      })
    },
    iniciaGestaoUsuarios () {
      this.processandoAcaoUsuarios = true
      var client = axios({
        method: 'post',
        url: 'https://procal.ipt.br/couch_user_manager/session',
        timeout: 5000,
        withCredentials: true,
        data: {
          username: this.usersForm.username,
          password: this.usersForm.password,
          db_name: window.localStorage.getItem('nomedb')
        }
      })
      .then((response) => {
        this.pegaSessaoGestaoUsuarios()
        this.pegaListaUsuarios()
      })
      .catch((error) => {
        //console.log(error.code)
        if (error.code === 'ECONNABORTED') {
          this.$toasted.show('Não foi possível iniciar a gestão de usuários agora. Tente novamente em breve.', {icon: 'fa-exclamation-triangle'})
        } else {
          this.$toasted.show('Usuário ou senha inválido', {icon: 'fa-exclamation-triangle'})
        }
        this.processandoAcaoUsuarios = false
        this.pegaSessaoGestaoUsuarios()
      })
    },
    pegaListaUsuarios () {
      var client = axios({
        method: 'get',
        url: 'https://procal.ipt.br/couch_user_manager/list_users',
        withCredentials: true,
        params: {
          db_name: window.localStorage.getItem('nomedb'),
        }
      })
      .then((response) => {
        this.userList = response.data.map((item) => {
          var res = {}
          res['name'] = item['name']
          res['admin'] = 
            item.roles.indexOf(window.localStorage.getItem('nomedb')+'__admin') !== -1
          ;
          return res
        }).filter((item) => {
          return item.name !== window.localStorage.getItem('nomedb')+'-admin'
        })
        this.processandoAcaoUsuarios = false
      })
      .catch((error) => {
        console.log(error)
        this.processandoAcaoUsuarios = false
        this.usuarioPodeGerirUsuarios = false
        this.apagaSessao()
        //this.pegaSessaoGestaoUsuarios()
      })
    },
    removeUsuario ($event) {
      this.instrumentState.modals.confirmarRemoverUsuario = true

      var promise = new Promise((resolve, reject) => {
        var btnConfirm = this.$refs.removeUsuarioOk
        var btnReject = this.$refs.removeUsuarioCancel

        'click ontouchstart'.split(' ').map((e) => {
          btnConfirm.addEventListener(e, () => {resolve('ok')})
          btnReject.addEventListener(e, () => {reject('ko')})
        })

      })

      promise.then(() => {
        this.processandoAcaoUsuarios = true
        var client = axios({
          method: 'delete',
          url: 'https://procal.ipt.br/couch_user_manager/remove_user',
          withCredentials: true,
          timeout: 5000,
          data: {
            db_name: window.localStorage.getItem('nomedb'),
            user_name: $event.target.getAttribute('user')
          }
        })
        .then((_response) => {
          this.$toasted.show('Usuário removido', {icon: 'fa-check-circle'})
          this.pegaListaUsuarios()
        })
        .catch((error) => {
          this.$toasted.show('error', {icon: 'fa-exclamation-triangle'})
          this.processandoAcaoUsuarios = false
          this.pegaSessaoGestaoUsuarios()
        })
      }).catch((err) => {
        console.log(err)
      }).finally(() => {
        this.instrumentState.modals.confirmarRemoverUsuario = false
      })
    },
    novoUsuario () {
      //inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
      this.processandoAcaoUsuarios = true
      var client = axios({
        method: 'post',
        url: 'https://procal.ipt.br/couch_user_manager/create_user',
        withCredentials: true,
        data: {
          db_name: window.localStorage.getItem('nomedb'),
          new_user_name: this.novoUsername,
          roles: [window.localStorage.getItem('nomedb')+'__member'].toString() // Passar como string
        }
      }).then((_response) => {
        this.$toasted.show('Usuário ' + this.novoUsername + ' criado. Aguardando confirmação.', {icon: 'fa-check-circle'})
        this.pegaListaUsuarios()
      }).catch((error) => {
        this.$toasted.show('Erro ao criar novo usuário.', {icon: 'fa-exclamation-triangle'})
        console.log(error)
        this.processandoAcaoUsuarios = false
        this.pegaSessaoGestaoUsuarios()
      }).finally(() => {
        this.instrumentState.modals.novoUsuarioDialog = false
        this.novoUsername = ''
      })
    },
    mudaRole ($event) {
      this.processandoAcaoUsuarios = true
      var el = $event.target.parentElement.getElementsByTagName('input')[0]
      var user_name = el.getAttribute('user')
      var seraAdmin = !el.checked
      
      var userItem = this.userList.filter((item) => {return item.name === user_name})
      userItem[0].admin = !userItem[0].admin
      
      var client = axios({
        method: 'post',
        url: 'https://procal.ipt.br/couch_user_manager/update_roles',
        withCredentials: true,
        timeout: 5000,
        data: {
          db_name: window.localStorage.getItem('nomedb'),
          user_name_to_manage: user_name,
          roles: window.localStorage.getItem('nomedb') + (seraAdmin ? '__admin' :'__member')
        }
      })
      .then((_response) => {
        this.$toasted.show('Permissão alterada.', {icon: 'fa-check-circle'})
      })
      .catch((error) => {
        this.$toasted.show('Erro ao alterar usuário.', {icon: 'fa-exclamation-triangle'})
        console.log(error)
        this.pegaSessaoGestaoUsuarios()
      })
      .finally( () => {
        this.$nextTick(() => {
          this.pegaListaUsuarios()
        })
      })
    },
    novaConfig (data, cb) {
      var timestamp = Date.now()

      var conf = _.cloneDeep(data)
      conf['_id'] = ''+this.instrumentState.data.inst._id+'_config_'+timestamp
      conf['autor'] = this.instrumentState.userCtx['name']
      conf['timestamp'] = timestamp
      conf['type'] = 'config_'+this.instrumentState.data.inst._id

      this.instrumentState.db.put(conf)
        .then(cb)
        .catch( (err) => {
          this.$toasted.show('Erro ao gravar.', {icon: 'fa-exclamation-triangle'})
        console.log(err)
      }).finally( () => {
      })

    },
    handleConfirmConfig () {
      var config = {}
      config.periodicidade = {
        val: parseInt(this.periodicidade.val),
        suffixo: 'months',
        status: this.periodicidade.status
      }

      //config.periodicidadeChecagem = this.periodicidadeChecagem;
      //config.alertaPeriodicidadeChecagem = this.alertaPeriodicidadeChecagem;

      /*var alteracaoPeriodocidade = periodicidadeVal - this.periodicidadeAnterior.val
      if (alteracaoPeriodocidade === 0 ) {
        this.$toasted.show('Periodicidade mantida.', {icon: 'fa-check-circle'})
        this.configDialog = false
        return
      }*/

      this.novaConfig(config, () => {
        this.$toasted.show('Periodicidade alterada.', {icon: 'fa-check-circle'})

        /*
        var alteracaoPeriodocidadeAbs = 
          Math.abs(alteracaoPeriodocidade)
        ;
        var mesOuMeses = 
          alteracaoPeriodocidadeAbs !== 1 ? 'meses' : 'mês'
        ;
        var aumentouOuDiminuiu = 
          alteracaoPeriodocidade > 0 ? 'diminuiu' : 'aumentou'
        ;
        let msg = 'A periodicidade ' + aumentouOuDiminuiu + 
          ' em ' + 
          alteracaoPeriodocidadeAbs.toString() + 
          ' ' +
          mesOuMeses
        ;
        this.$toasted.show(msg, {icon: 'fa-check-circle'})
        */
      })

      this.instrumentState.modals.configDialog = false
    },
    setLoggedOut () {
      window.localStorage.removeItem('nomedb')
      window.localStorage.removeItem('userCtx')

      var el = document.getElementById('pdf-container')
      el.style.display = 'none'

      INSTRUMENT_STORE.state.db = null
      
      if (!INSTRUMENT_STORE.remoteDB) return
      
      INSTRUMENT_STORE.remoteDB.logOut( (err, response) => {
        if (err) {
          console.log(err)
        }
        /*if (response && response.ok) {

        }*/
      })      
    }

  },
  computed: {
    temUltimoCertificado () {
      return INSTRUMENT_STORE.state.temUltimoCertificado === true
    },    
    textoBotaoAcessar () {
      if (this.$route.query && this.$route.query.token) {
        return 'Definir senha'
      }
      return 'Acessar'
    },
    novoInstrumentoValido () {
      
      var campos = ['descricao','modelo','fabricante']
      for (var i = 0; i < campos.length; i++) {
        if (_.trim(this.formNovoInstrumento[campos[i]]).length === 0) {
          return false
        }
      }
      if (!this.formNovoInstrumento.AlTipoPeriodo || !this.formNovoInstrumento.AlPeriodoCalibracao) {
        return false
      }
      
      var calibracoes = this.formNovoInstrumento.calibracoes      
      if (!this.formNovoInstrumento.calibracoes.length) {
        return false
      }

      var campos = ['Certificado', 'HoraInicial']

      for (var i = 0; i < calibracoes.length; i++) {
        for (var j = 0; j < campos.length; j++) {
          if (_.trim(calibracoes[i][campos[j]]).length === 0) {
            return false
          }
        }
      }
      

      return true
    },
    listaModelos () {
      return _.uniq(Object.keys(this.instrumentState.instrumentos).map((k) => this.instrumentState.instrumentos[k].item.modelo))
    },
    temCalibracaoComResultados () {
      return !!this.instrumentState.data.seriesDenormRes && this.instrumentState.data.seriesDenormRes.length
    },
    agora () {
      return moment().format('MMM YYYY')
    },
    confLog () {
      if (!this.instrumentState.data.ocorr || !this.instrumentState.data.config || !this.instrumentState.funcoes) return []
      
      // Extende objeto
      Object.keys(this.instrumentState.funcoes).map((k) => {
        window.T[k] = this.instrumentState.funcoes[k].desc
      })

      var res = []
        
      var itemAnterior = null
      var configSorted = this.instrumentState.data.config
      this.instrumentState.faixas.concat([{CodigoFaixaModelo: 'global', DescFaixa: 'Análise Global'}]).map((faixa) => {
        itemAnterior = null
        var codFaixa = faixa.CodigoFaixaModelo
        configSorted
          .filter((item) => item.analise && item.analise[codFaixa])
          .map((item) => {
            var dataMoment = moment(item.timestamp)
            var obj = {
              alteracoes: [],
              icon: 'sliders-h',
              obs: item.observacao,
              definicoes: [],
              categorias: [],
              autor: item.autor,
              data: item.timestamp,
              titulo: faixa.DescFaixa,
              ano: dataMoment.year(),
              dataFmt: dataMoment.format('DD/MM/YYYY')
              //timeDelta: moment.duration(Date.now()-item.timestamp).humanize()
            }
            var ana = item.analise[codFaixa]
            Object.keys(ana)
              .filter((k) => ['timestamp'].indexOf(k) === -1)
              .map((k) => {
                if (itemAnterior && itemAnterior[k] !== ana[k] && !_.isNil(itemAnterior[k]) && !_.isNil(ana[k])) {
                  obj.alteracoes.push(
                    [
                      window.T[k],
                      ' mudou de ',
                      window.T[itemAnterior[k]] || itemAnterior[k],
                      ' para ',
                      window.T[ana[k]] || ana[k]
                    ]
                  )
                }
                else if ( (!itemAnterior && !_.isNil(ana[k])) || (itemAnterior && _.isNil(itemAnterior[k]) && !_.isNil(ana[k]) && _.isNil(window.T[ana[k]])) ) {
                  obj.definicoes.push([window.T[k] || k, ' foi definido para ', window.T[ana[k]] || ana[k]])
                }
              })
            ;

            res.push(obj)
            itemAnterior = ana
          })
        ;


      })

      itemAnterior = {"periodicidade":{"val":12,"suffixo":"months","status":"Ativo"},"periodicidadeAlerta":{"val":2,"suffixo":"months"}}
      configSorted
        .filter((item) => item.periodicidade)
        .map((item) => {
          var dataMoment = moment(item.timestamp)
          var obj = {
            alteracoes: [], 
            icon: 'calendar-day',
            obs: null,
            definicoes: [],
            categorias: [],
            autor: item.autor,
            data: item.timestamp,
            titulo: null,
            ano: dataMoment.year(),
            dataFmt: dataMoment.format('DD/MM/YYYY')
            //timeDelta: moment.duration(Date.now()-item.timestamp).humanize()
          }
          if (itemAnterior && itemAnterior.periodicidade.val !== item.periodicidade.val) {
            var textoDataAnterior = ((itemAnterior ? itemAnterior.periodicidade.val : '12') + ' meses')
            obj.alteracoes.push(['Periodicidade', 'mudou de ', ''+textoDataAnterior, ' para ', item.periodicidade.val+' meses'])
          }

          if (itemAnterior && itemAnterior.periodicidade.status !== item.periodicidade.status && item.periodicidade.status) {
            obj.alteracoes.push(['Status', 'mudou de ', ''+(itemAnterior.periodicidade.status || 'Ativo'), ' para ', item.periodicidade.status])
          }

          if (obj.alteracoes.length) {
            res.push(obj)
          }

          itemAnterior = item
        })
      ;

      this.instrumentState.data.ocorr
        .map((item) => {
          var dataMoment = moment(item.timestamp)
          var obj = {
            id: item._id,
            alteracoes: [], 
            icon: item.dataDaProximaChecagem ? 'check-double' : 'edit',
            obs: item.desc,
            definicoes: [],
            categorias: item.categorias,
            autor: item.autor,
            data: item.timestamp,
            titulo: null,
            ano: dataMoment.year(),
            dataFmt: dataMoment.format('DD/MM/YYYY')
            //timeDelta: moment.duration(Date.now()-item.timestamp).humanize()
          }
          res.push(obj)
        })

      var calMap = {}
      for (var i = INSTRUMENT_STORE.state.queryData.length - 1; i >= 0; i--) {
        var item = INSTRUMENT_STORE.state.queryData[i]
        if (!calMap[item.Certificado] && !item.mock) {
          var dataMoment = moment(item.HoraInicial)
          var obj = {
            alteracoes: [], 
            icon: 'check',
            obs: 'Calibração executada. Certificado '+ item.Certificado,
            definicoes: [],
            categorias: [],
            autor: null,
            data: dataMoment.unix()*1000,
            titulo: null,
            ano: dataMoment.year(),
            dataFmt: dataMoment.format('DD/MM/YYYY')
            //timeDelta: moment.duration(Date.now()-item.timestamp).humanize()
          }

          res.push(obj)
          
          calMap[item.Certificado] = true
        }
      }

      var primeiroReg = INSTRUMENT_STORE.state.queryData[INSTRUMENT_STORE.state.queryData.length - 1]

      var primeiroRegMoment = moment(primeiroReg.HoraInicial)
      var agoraMoment = moment()
      
      var marcadoresDeAno = []
      for (var ano = primeiroRegMoment.year(); ano <= agoraMoment.year(); ano++) {
        marcadoresDeAno.push({type: 'marcadorDeAno', ano: ano, data: moment(''+ano+'-01-01T00:00:00-06:00').unix()*1000})
      }

      // Remove marcadores que não possuem dados no ano
      marcadoresDeAno.filter((item) => {
        return !!res.filter((resItem) => item.ano === resItem.ano).length
      })
      .map((item) => {
        res.push(item)
      })

      return _.reverse(_.sortBy(res, 'data'))
    },
    // periodicidadeVal () {
    //   if (this.instrumentState.configPeriodicidade) {
    //     return this.instrumentState.configPeriodicidade.periodicidade.val
    //   }
    // },
    instId () {
      return this.$route.params.id
    },
    emInstrumento () {
      return this.$route.path.split('/')[1] === 'instrumento'
    },
    rotaInst () {
      var res = this.$route.path.split('/')[3]
      if (this.emInstrumento && res === undefined) {
        return 'certificado'
      }
      return res
    },
    alertaInstrumento () {
      if (this.instId) {
        return this.instrumentState.instrumentos[''+this.instId]
      }
    },
    instrumentoStatus () {
      if (this.alertaInstrumento) {
        return this.alertaInstrumento.status
      }
    },
    ultimoCertificado () {
      if (!this.instrumentState.data.seriesDenormRes) {
        return '-'
      }

      if (!this.instrumentState.data.seriesDenormRes.length) {
        return '-'
      }

      var serie = this.instrumentState.data.seriesDenormRes.filter((item) => {
        return item.HoraInicial === this.alertaInstrumento.status.dataUltimaCal
      })[0]

      if (!serie) {
        return '-'
      }

      return serie['Certificado']
    },
    ocorrenciaDeChecagemIntermediaria () {
      return this.categoriasSelecionadas.indexOf('Checagem Intermediária') !== -1
    },
    permiteGravarOcorrencia () {
      if (this.ocorrenciaDeChecagemIntermediaria) {
        if (!!this.dataDaChecagem && !!this.dataDaProximaChecagem) {
          return true;
        }
        return false;
      }
      return this.categoriasSelecionadas.length > 0 || this.ocorrenciaDesc.length;
    },
    statusChecagem () {
      return fnPegaStatusChecagem(this.instrumentState.data.ocorr.filter((item) => item.dataDaProximaChecagem));
    }

  }

}
</script>

