const routes = [
  {
    path: '/instrumento/:id',
    component: Instrument,
    //component: () => import(/ webpackChunkName: "Instrument" / '@/components/Instrument.vue'),
    name: 'instrumento',
    children: [
      {
        path: 'analise',
        component: InstrumentAnalise,
        //component: () => import(/ webpackChunkName: "InstrumentAnalise" / '@/components/InstrumentAnalise.vue'),
        children: [
          {
            path: ':CodigoFaixaModelo',
            props: true
          }
        ],
        props: true
      },
      {
        path: 'certificado',
        component: InstrumentCertificado,
        //component: () => import(/ webpackChunkName: "InstrumentCertificado" / '@/components/InstrumentCertificado.vue'),
        props: true
      },
      {
        path: '',
        component: InstrumentCertificado,
        //component: () => import(/ webpackChunkName: "InstrumentCertificado" / '@/components/InstrumentCertificado.vue'),
        props: true
      }
    ]
  },
  {
    path: '/instrumentos',
    component: Instrumentos,
    //component: () => import(/ webpackChunkName: "Instrumentos" / '@/components/Instrumentos.vue'),
    name: 'instrumentos'
  }
  ,
  {
    path: '/landing',
    component: Landing,
    //component: () => import(/ webpackChunkName: "Landing" / '@/components/Landing.vue'),
    name: 'landing'    
  }
]

const router = new VueRouter({
  /*mode: 'history',*/
  routes: routes,
  linkActiveClass: 'is-active'
})


export default router
