"use strict";
function Analises(data, faixaSelecionada, analiseFaixaArg) {
  var self = this;
  this.data = data;
  this.faixaSelecionada = faixaSelecionada || undefined;
  // Itera o inverso das configurações disponiveis 
  // desta forma pegando apenas as ultimas configuracoes
  var item;
  this.analise = {}
  var analiseGlobal = null
  //self.data.config = self.data.config.sort(function(a, b) {return a.timestamp - b.timestamp})
  for (var i = self.data.config.length - 1; i >= 0; i--) {
    item = self.data.config[i].analise
    if (item) {
      if (item.global && !analiseGlobal) {
        analiseGlobal = item.global
      }
      var itemKeys = Object.keys(item)
      for (var j = 0; j < itemKeys.length; j++) {
        var codigoFaixa = itemKeys[j]
        if (!self.analise[codigoFaixa]) {
          self.analise[codigoFaixa] = item[codigoFaixa]
        }
      }
    }
  }
  
  var analiseFaixaStore = this.analise[self.faixaSelecionada];

  if (analiseGlobal) {
    // Pega se nao tiver analise registrada para a faixa
    if (!analiseFaixaStore) {
      analiseFaixaStore = analiseGlobal
    } else {
      // Pega se este for mais atual que a analise da faixa
      if (analiseGlobal.timestamp > analiseFaixaStore.timestamp) {
        analiseFaixaStore = analiseGlobal
      }
    }
  }

  if (analiseFaixaArg) {
    this.analiseFaixa = analiseFaixaArg
  } else if (analiseFaixaStore) {
    this.analiseFaixa = analiseFaixaStore;
  } else {
    this.analiseFaixa = {
      aplicarCorrecao: false,
      lhs: "ema",
      fatorMultiplicativo: 1,
      tolVal: null,
      tolValNaUnidade: 0,
      tolUnidade: null
    };
  }

  var tolVal = parseFloat(this.analiseFaixa.tolVal);
  tolVal = isNaN(tolVal) ? 0 : tolVal;
  this.analiseFaixa.tolVal = tolVal
  this.analiseFaixa.tolValNaUnidade = this.analiseFaixa.tolValNaUnidade || 0
  this.analiseFaixa.tolUnidade = this.analiseFaixa.tolUnidade || '%'
  
  this.analiseNotas = "";

  function pegaValorUsado(ResultadoMedicao, aplicarCorrecao) {
    var valorUsado;

    if (aplicarCorrecao) {
      valorUsado = 0;
    } else {
      valorUsado = Math.abs(ResultadoMedicao);
    }

    return valorUsado;
  }

  /*
  * TODO: Verificar de outra forma a conformidade
  * quando a tolerancia e calculada com base
  * em um valor 0
  */

  function pontoEhConforme(analise, tol) {
    var analise_0 = parseFloat(analise[0].toExponential(2));
    var tol_0 = parseFloat(tol[0].toExponential(2));
    var analise_1 = parseFloat(analise[1].toExponential(2));
    var tol_1 = parseFloat(tol[1].toExponential(2));
    return analise_0 >= tol_0 && analise_1 <= tol_1;
  }

  function sumsqrd(arr) {
    var sum = 0;
    var i = arr.length;

    while (--i >= 0) {
      sum += arr[i] * arr[i];
    }

    return sum;
  }

  function anaSomaQuad(series, criterio) {
    var resSeries = series.map(function(s) {
      var fm = criterio.fatorMultiplicativo;
      var d = s.data;
      var res = [];

      for (var i = 0; i < d.ResultadoMedicao.length; i++) {
        var valorUsado = pegaValorUsado(
          d.ResultadoMedicao[i],
          criterio.aplicarCorrecao
        );
        var ema = d.ErroMaximoFaixa[i];
        var maxIncCal = d.IncertExpandidaUsada[i];
        var k = d.fatork ? d.fatork[i] : 2; //TODO exportar fator k

        res.push([
          (Math.sqrt(sumsqrd([maxIncCal / k, ema / sqrt3])) * sqrt3 +
            valorUsado) *
            fm,
          (Math.sqrt(sumsqrd([maxIncCal / k, ema / sqrt3])) * sqrt3 +
            valorUsado) *
            fm
        ]);
      }

      return res;
    });
    return {
      res: resSeries,
      notas: null
    };
  }

  function anaSomaLinear(series, criterio) {
    var resSeries = series.map(function(s) {
      var fm = criterio.fatorMultiplicativo;
      var d = s.data;
      var res = [];

      for (var i = 0; i < d.ResultadoMedicao.length; i++) {
        var valorUsado = pegaValorUsado(
          d.ResultadoMedicao[i],
          criterio.aplicarCorrecao
        );
        var ema = d.ErroMaximoFaixa[i];
        var maxIncCal = d.IncertExpandidaUsada[i];
        var k = d.fatork ? d.fatork[i] : 2; //TODO exportar fator k

        res.push([
          (maxIncCal + valorUsado) * fm,
          (maxIncCal + valorUsado) * fm
        ]);

      }

      return res;
    });
    return {
      res: resSeries,
      notas: null
    };
  }

  var sqrt3 = Math.sqrt(3);

  this.seriesFn = function() {
    return self.data.seriesDenormRes;
  };

  this.seriesFaixaFn = function() {
    return self.series.filter(function(item) {
      return item.CodigoFaixaModelo === self.faixaSelecionada;
    });
  };

  this.seriesFaixaUltimaCalFn = function() {
    var h = self.seriesFaixa[0].HoraInicial;
    return self.seriesFaixa.filter(function(item) {
      return item.HoraInicial === h;
    });
  };

  this.dadosFaixaFn = function() {
    var res = {};
    var series = self.seriesFaixaUltimaCal.map(function(item) {
      return {
        nome: item.nomeSerie,
        cod: item.cod,
        data: item.data,
        sortVal: null
      };
    });
    ["CodigoFaixaModelo", "DescFaixa", "Prefixo", "Unidade"].map(function(
      item
    ) {
      res[item] = self.seriesFaixaUltimaCal[0][item];
    });
    res.series = series;
    return res;
  };

  this.toleranciaCalculadaFn = function() {
    var resTol = self.seriesFaixaUltimaCal.map(function(s, s_i) {
      var d = s.data;
      var n = d.ResultadoMedicao.length;
      var res = [];
      var ValInstAbs;
      var fatorTol;
      var tolConst = 0;

      if (self.analiseFaixa.lhs === "auto") {
        fatorTol = self.analiseFaixa.tolVal / 100;
      } else {
        if (self.analiseFaixa.tolUnidade === "%") {
          fatorTol = self.analiseFaixa.tolVal / 100;
        } else if (self.analiseFaixa.tolUnidade === "ppm") {
          fatorTol = self.analiseFaixa.tolVal / 1e6;
        }

        var tolConstPre = parseFloat(self.analiseFaixa.tolValNaUnidade);

        if (isNaN(tolConstPre)) {
          tolConst = 0;
        } else {
          tolConst = tolConstPre;
        }
      }

      if (self.analiseFaixa.lhs === "tol") {
        for (var i = 0; i < n; i++) {
          ValInstAbs = Math.abs(d.ValorDoInstrumento[i]);
          res.push([
            -ValInstAbs * fatorTol - tolConst,
            ValInstAbs * fatorTol + tolConst
          ]);
        }
      } else if (self.analiseFaixa.lhs === "ema") {
        // ema
        for (var i = 0; i < n; i++) {
          res.push([d.ErroMinimoFaixa[i], d.ErroMaximoFaixa[i]]);
        }
      } else {
        var analisesSeries = self.funcoes[self.analiseFaixa.lhs].func(
          self.seriesFaixaUltimaCal,
          self.analiseFaixa
        );
        var analises = analisesSeries.res;
        self.analiseNotas = analisesSeries.notas;

        for (var i = 0; i < s.data.Valor.length; i++) {
          var analisePre = analises[s_i][i];
          var analise = [-analisePre[0], analisePre[1]];
          res.push(analise);
        }
      }

      return res;
    });
    return resTol;
  };

  this.constantesComputadoFn = function() {
    return self.seriesFaixaUltimaCal.map(function(s) {
      var d = s.data;
      var n = d.ErroMinimoFaixa.length;
      var res = [];

      for (var i = 0; i < n; i++) {
        var obj = {};
        obj.ema = [d.ErroMinimoFaixa[i], d.ErroMaximoFaixa[i]];
        obj.ValorDoInstrumento = d.ValorDoInstrumento[i];
        obj.Valor = d.Valor[i];
        obj.ResultadoMedicao = d.ResultadoMedicao[i];
        obj.ValorDoPadrao = d.ValorDoPadrao[i];
        obj.IncertExpandidaUsada = d.IncertExpandidaUsada[i];
        res.push(obj);
      }

      return res;
    });
  };

  this.resultadoAnalisesFn = function() {
    var analisesSeries = self.funcoes[self.analiseFaixa.lhs].func(
      self.seriesFaixaUltimaCal,
      self.analiseFaixa
    );

    var analises = analisesSeries.res;
    self.analiseNotas = analisesSeries.notas;
    var resAnalise = self.seriesFaixaUltimaCal.map(function(serie, serie_i) {
      var res = [];

      for (var i = 0; i < serie.data.Valor.length; i++) {
        var analisePre = analises[serie_i][i];
        var analise = [-analisePre[0], analisePre[1]];
        var tol = self.toleranciaCalculada[serie_i][i];
        var tolNuloSeZero = tol[0] === 0 && tol[1] === 0 ? [null, null] : tol;
        var obj = {
          analise: analise,
          tol: tolNuloSeZero,
          pontoConforme: pontoEhConforme(analise, tol),
          aplicarCorrecao: self.analiseFaixa.aplicarCorrecao
        };
        Object.keys(serie).map(function(key) {
          if (typeof serie[key] === "string") {
            obj[key] = serie[key];
          }
        });
        var constantesComputadoSeriePonto =
          self.constantesComputado[serie_i][i];
        Object.keys(constantesComputadoSeriePonto).map(function(key) {
          obj[key] = constantesComputadoSeriePonto[key];
        });
        res.push(obj);
      }

      return res;
    });
    return resAnalise;
  }; // Customizavel para cliente

  this.executa = function() {
    self.series = self.seriesFn()
    self.seriesFaixa = self.seriesFaixaFn()
    self.seriesFaixaUltimaCal = self.seriesFaixaUltimaCalFn()
    self.dadosFaixa = self.dadosFaixaFn()
    self.toleranciaCalculada = self.toleranciaCalculadaFn()
    self.constantesComputado = self.constantesComputadoFn()
    self.resultadoAnalises = self.resultadoAnalisesFn()
    return self.resultadoAnalises
  }

  this.funcoes = {
    // Registra analises padrao
    ema: {
      desc: "Certificado de Calibração",
      tipoAnalise: 'criterioAceitacao',
      func: anaSomaLinear
    },
    tol: {
      desc: "Tolerância",
      tipoAnalise: 'criterioAceitacao',
      func: anaSomaLinear
    },
    somaQuadUEMAMaisE: {
      desc: "Análise por faixa - Caso B",
      detalhes: "",
      detalhesExtra: "",
      tipoAnalise: "incertezaASerUtilizada",
      func: function (series, criterio) {
        var _fm = criterio.fatorMultiplicativo; // Necessário para as notas não sumirem quando o usuário altera o fator

        var driftNota = "<b>Variação da Nova Incerteza em relação ao EMA do fabricante:</b>\n";
        var resSeries = series.map(function (s) {
          var d = s.data;
          var res = [];

          for (var i = 0; i < d.ResultadoMedicao.length; i++) {
            var ponto = d.Valor[i] + " " + s.Prefixo + s.Unidade;
            var nomeSerie = series.length !== 1 ? " (" + s.nomeSerie + ")" : "";
            var erro = pegaValorUsado(d.ResultadoMedicao[i], criterio.aplicarCorrecao);
            var maxIncCal = Math.abs(d.IncertExpandidaUsada[i]);
            var ema = Math.abs(d.ErroMaximoFaixa[i]); // Pelo critério do laboratório, aplicar apenas erro se não estiver conforme

            var componenteErro = erro + maxIncCal <= ema ? 0 : erro;
            var k = d.fatork[i] || 2;
            var ana = (Math.sqrt(sumsqrd([maxIncCal / k, ema / sqrt3])) * sqrt3 + componenteErro) * 0.95;
            var variacao = (ana - ema) / ema * 100;

            if (variacao > 0) {
              variacao = '<span style="color: red;">' + parseFloat(variacao.toPrecision(2)) + " %</span>";
            } else {
              variacao = '<span style="color: green;">' + parseFloat(variacao.toPrecision(2)) + " %</span>";
            }

            driftNota += "" + ponto.toString() + ": " + variacao + nomeSerie + "\n";
            res.push([ana, ana]);
          }

          return res;
        });
        return {
          res: resSeries,
          notas: driftNota
        };
      }
    },
    AvaliacaoVolks: {
      desc: 'Avaliação Volks',
      detalhes: '',
      detalhesExtra: '',
      tipoAnalise: 'incertezaASerUtilizada',
      func: function (series, criterio) {
        var VMarr = [];
        var resSeries = series.map( function(s, s_i) {
          const d = s.data;
          var res = [];

          for (var i = 0; i < d.ResultadoMedicao.length; i++) {
            var valorUsado = pegaValorUsado(d.ResultadoMedicao[i], criterio.aplicarCorrecao);
            var ema = d.ErroMaximoFaixa[i];
            var maxIncCal = d.IncertExpandidaUsada[i];
            var vInst = d.ValorDoInstrumento[i];
            VMarr.push(Math.sqrt(sumsqrd([Math.abs(maxIncCal / vInst) * 100, Math.abs(valorUsado / vInst) * 100])));
            var sq = Math.sqrt(sumsqrd([maxIncCal, valorUsado]));
            res.push([sq, sq]);
          }

          return res;
        });
        var analise = '';
        var VMArrNumerosValidos = VMarr.filter(function (item) {
          return isFinite(item) && typeof item === 'number';
        });
        var maxVM = Math.max.apply(undefined, VMArrNumerosValidos);

        if (!maxVM) {
          analise = 'Não aplicável';
        } else {
          var classe, nivel;

          if (maxVM <= 0.001) {
            classe = '00';
            nivel = '1';
          } else if (maxVM <= 0.01) {
            classe = '0';
            nivel = '2';
          } else if (maxVM <= 0.1) {
            classe = '1';
            nivel = '3';
          } else if (maxVM <= 0.5) {
            classe = '2';
            nivel = '4';
          } else if (maxVM <= 1) {
            classe = '3';
            nivel = '5';
          } else if (maxVM <= 3) {
            classe = '4';
            nivel = '6';
          } else if (maxVM <= 5) {
            classe = '5';
            nivel = '7';
          } else {
            classe = '6';
            nivel = '8';
          }

          analise += 'Classe de exatidão: ' + classe + '\n';
          analise += 'Nível hierárquico: ' + nivel + '\n';
        }

        return {
          res: resSeries,
          notas: analise
        };
      }
    }


  };
}

var couchFnReduce = function(keys, values, rereduce) { // manter esta linha neste formato
  // A função `Analises` será injetada no navegador utilizando `Function()`,
  // pois assim o usuário poderá testar em tempo real e offline o resultado
  // para diversas configurações de análise enviada via `analiseFaixaArg`
  
  /**
  Denormaliza os dados das calibrações
  obtendo o seguinte schema:

  [
    {
      "Certificado": "166317-101",
      "CertificadoAl": "166317-101",
      "HoraInicial": "2018-06-19 00:00:00",
      "CodigoFaixaModelo": "3528",
      "DescFaixa": "8 °C a 31,5 °C",
      "Freq": "",
      "FreqPrefixo": "",
      "Prefixo": "",
      "Unidade": "°C",
      "cod": "serie",
      "data": {
        "IncertExpandidaUsada": [
          0.04,
          0.04,
          0.04
        ],
      
        ...
      
      "nomeSerie": "Série"
    },
    {
      "Certificado": "152121-101",
      
      ...

    }
  ]
  */
  var codigosFaixas = {}
  var seriesDenorm = function(calibracoes) {
    var cal
    var faixa, faixas
    var serie, series
    var row
    var res = []
    var i, j, k, l
    var key
    var calKeys = ['Certificado', 'CertificadoAl', 'HoraInicial']
    var faixaKeys = ['CodigoFaixaModelo', 'DescFaixa', 'Freq', 'FreqPrefixo', 'Prefixo', 'Unidade']
    var serieKeys = ['cod', 'data']
    
    var historico = {}
    
    if (!calibracoes) return []
    for (i = 0; i < calibracoes.length; i++) {
      cal = calibracoes[i]
      faixas = cal.Faixas
      for (j = 0; j < faixas.length; j++) {
        faixa = faixas[j]
        series = faixa.series
        codigosFaixas[faixa.CodigoFaixaModelo] = true
        for (k = 0; k < series.length; k++) {
          serie = series[k]
          row = {}
          
          for (l = 0; l < calKeys.length; l++) {
            key = calKeys[l]
            row[key] = cal[key]
          }
          
          for (l = 0; l < faixaKeys.length; l++) {
            key = faixaKeys[l]
            row[key] = faixa[key]
          }

          for (l = 0; l < serieKeys.length; l++) {
            key = serieKeys[l]
            row[key] = serie[key]
          }
          row['nomeSerie'] = serie['nome']

          res.push(row)
        }
      }
    }
    return res
  }

  /**
  Denormaliza resultados das calibracoes obtendo o seguinte schema:

  [
    {
      "cod": "serie",
      "CodigoFaixaModelo": "3528",
      "HoraInicial": "2018-06-19 00:00:00",
      ...
      "Valor": 22,
      "fatork": 2
    },
    ...
  ]  
  */
  var serieResultDenorm = function(seriesDenormData) {
    // Adiciona historico para cada serie
    var res = []
    for (var i = 0; i < seriesDenormData.length; i++) {
      var row = seriesDenormData[i]
      

      var keys = Object.keys(row.data)
      for (var j = 0; j < row.data.Valor.length; j++) {
        var obj = {
          cod: row.cod,
          CodigoFaixaModelo: row.CodigoFaixaModelo,
          HoraInicial: row.HoraInicial
        }
        for (var k = 0; k < keys.length; k++) {
          obj[keys[k]] = row.data[keys[k]][j]
        }
        res.push(obj)
      }
    }
    return res
  }

  /**
  Altera o objeto `seriesDenormRes` adicionando os dados históricos.
  schema:

  [
    {
      ...
      "nomeSerie": "Série"
      "historicosPontos": [
        [
          {
            "HoraInicial": "2016-06-14 00:00:00",
            "nomeSerie": "Série",
            "serie": {
              "ErroMaximoFaixa": 0.1,
              "ErroMinimoFaixa": -0.1,
              "IncertExpandidaUsada": 0.05,
              "ResultadoMedicao": 0.09,
              "Valor": 22,
              "ValorDoInstrumento": 22.1,
              "ValorDoPadrao": 22,
              "fatork": 2
            },
            ...
          }
        ],
      ...
    },
    ...
  ]

  */
  
  var adicionaHistoricoDosPontos = function(seriesDenormRes, serieResultDenormRes) {
    var histCache = {}
    for (var i = 0; i < seriesDenormRes.length; i++) {
      var row = seriesDenormRes[i]
      row.historicosPontos = []
      for (var j = 0; j < row.data.Valor.length; j++) {
        var valor = row.data.Valor[j]
        var codigoFaixa = row.CodigoFaixaModelo
        var cod = row.cod
        var valorHistCacheId = ''+valor+'__'+codigoFaixa+'___'+cod
        var histValorFaixa
        if (histCache[valorHistCacheId]) {
          histValorFaixa = histCache[valorHistCacheId]
        } else {
          histValorFaixa = serieResultDenormRes.filter(function(item) {
            return item.Valor === valor &&
              item.CodigoFaixaModelo === codigoFaixa &&
              item.cod === cod
            ;
          }).map(function(item) {
            var obj = {
              HoraInicial: item.HoraInicial,
              nomeSerie: row.nomeSerie,
              serie: {}
            }
            var keys = ['ErroMaximoFaixa', 'ErroMinimoFaixa', 'IncertExpandidaUsada', 'ResultadoMedicao', 'Valor', 'ValorDoInstrumento', 'ValorDoPadrao', 'fatork']
            for (var i = 0; i < keys.length; i++) {
              obj.serie[keys[i]] = item[keys[i]]
            }
            return obj
          })
          histCache[valorHistCacheId] = histValorFaixa
        }
        row.historicosPontos.push(histValorFaixa)
      }
    }
  }

  var i;
  var res = {
    inst: {},
    config: [],
    ocorr: [],
    analise: {}
  };

  var calibracoes
  if (rereduce) {
    if (values) {
      for(i = 0; i < values.length; i++) {
        if (values[i] && values[i].inst && values[i].inst.calibracoes) {
          calibracoes = values[i].inst.calibracoes
          var keys = Object.keys(values[i].inst).filter(function(item) {return item !== 'calibracoes'})
          for (var j = 0; j < keys.length; j++) {
            var key = keys[j]
            res.inst[key] = values[i].inst[key]
          }
        }
        if (values[i] && values[i].config && values[i].config.length) {
          res.config.push.apply(res.config, values[i].config);
        }
        if (values[i] && values[i].ocorr && values[i].ocorr.length) {
          res.ocorr.push.apply(res.ocorr, values[i].ocorr);
        }
      }
    }

    // garante array de configuracoes na ordem correta
    if (res.config.length) {
      res.config = res.config.sort(function(a, b) {
        return a.timestamp - b.timestamp
      })
    }
    
    var configsPeriodicidade = res.config.filter(function(item){return item.periodicidade})
    var periodicidadeMeses = 12
    var instStatus = 'Ativo'
    if (configsPeriodicidade.length) {
      periodicidadeMeses = configsPeriodicidade[configsPeriodicidade.length-1].periodicidade.val
      instStatus = configsPeriodicidade[configsPeriodicidade.length-1].periodicidade.status || 'Ativo'
    }

    var seriesDenormRes = seriesDenorm(calibracoes)
    //console.log(seriesDenormRes)
    var serieResultDenormRes = serieResultDenorm(seriesDenormRes)
    //console.log(serieResultDenormRes)
    adicionaHistoricoDosPontos(seriesDenormRes, serieResultDenormRes)
    //console.log(seriesDenormRes)
    res.seriesDenormRes = seriesDenormRes
    // Aqui devemos verificar se estamos no browser ou no Couchdb.
    if (typeof window === "undefined" || typeof MOCK_COUCH === "boolean") { // Couch
      var codigosFaixasArr = Object.keys(codigosFaixas)
      var faixasReduce = []
      for (var i = 0; i < codigosFaixasArr.length; i++) {
        var ana = new Analises(res, codigosFaixasArr[i])
        ana.executa()

        // Normaliza os dados
        var faixaReduce = {}
        var faixaKeys = ['DescFaixa', 'Certificado', 'HoraInicial', 'CodigoFaixaModelo', 'Unidade', 'Prefixo', 'aplicarCorrecao']
        faixaKeys.map(function(item) {
          faixaReduce[item] = ana.resultadoAnalises[0][0][item]
        })

        faixaReduce['periodicidadeMeses'] = periodicidadeMeses
        faixaReduce['status'] = instStatus

        faixaReduce.series = []
        ana.resultadoAnalises.map(function(serie) {
          var serieReduce = {}
          serieReduce.nomeSerie = serie[0].nomeSerie
          serieReduce.pontos = []
          serie.map(function(ponto) {
            var pontoReduce = {}
            var pontoKeys = ['pontoConforme', 'Freq', 'FreqPrefixo', 'ema', 'tol', 'ValorDoInstrumento', 'Valor', 'ResultadoMedicao', 'ValorDoPadrao', 'IncertExpandidaUsada']
            pontoKeys.map(function(item) {
              pontoReduce[item] = ponto[item]
            })
            serieReduce.pontos.push(pontoReduce)
          })
          faixaReduce.series.push(serieReduce)
        })
        faixasReduce.push(faixaReduce)
      }
      return faixasReduce
    } else { // Browser
      return res
    }

  } else {
    for(i = 0; i < values.length; i++) {
      if (keys[i][0][1] === 0) {
        res.inst = values[i]
      }
      if (keys[i][0][1] === 1) {
        res.config.push(values[i])
      }
      if (keys[i][0][1] === 2) {
        res.ocorr.push(values[i])
      }
    }
    return res;
  }
}

export {Analises, couchFnReduce}