        })
        .catch((error) => {
          console.log(error)
          /*this.$toasted.show('Não foi possível definir a senha. Caso já tenha definido a senha, clique em login. Mensagem do erro: '+error['message'], {
            icon: 'error'
          })*/
          this.$toasted.show('Não foi possível definir a senha. Caso já tenha feito isto anteriormente, clique em "ir para login"', {
            icon: 'error'
          })
        })
        .finally(() => {
          this.processando = false
        })
      }, 100)
    },
    submitLogin() {
      var valid = true
      if (valid) {
        localStorage.setItem('ms_username', this.username);
        
        this.processando = true
        
        window.setTimeout(() => {
          this.loginDB.login(this.username, this.password)
          .then((response) => {
            if(!response) return
            
            if (response.ok) {
              this.$emit('check_login')
            }
          })
          .catch((err) => {
            console.log(err)
            if (err.name === 'unauthorized') {
              this.$toasted.show('Usuário ou senha inválido', {icon: 'exclamation-circle'} )
            } else {
              this.$toasted.show('Não foi possível entrar agora, tente em breve. Nome do erro: '+err.name, {icon: 'exclamation-circle'} )
            }
          })
          .finally(() => {
            window.setTimeout(() => {
              this.processando = false
            }, 2000)
          })
        }, 50)

        
      } else {
        console.log('error submit!!');
        return false;
      }
    }
  },
  computed: {
    scorePassword () {
        var score = 0;
        if (!this.password)
            return score;

        // award every unique letter until 5 repetitions
        var letters = new Object();
        for (var i=0; i<this.password.length; i++) {
            letters[this.password[i]] = (letters[this.password[i]] || 0) + 1;
            score += 5.0 / letters[this.password[i]];
        }

        // bonus points for mixing it up
        var variations = {
            digits: /\d/.test(this.password),
            lower: /[a-z]/.test(this.password),
            upper: /[A-Z]/.test(this.password),
            nonWords: /\W/.test(this.password),
        }

        var variationCount = 0;
        for (var check in variations) {
            variationCount += (variations[check] == true) ? 1 : 0;
        }
        score += (variationCount - 1) * 10;

        return parseInt(score);
    },
    solicitarValid () {
      return this.username !== ''
    },
    loginValid () {
      return this.solicitarValid && this.password !== ''
    },
    definirValid () {
      return this.password !== '' && this.password === this.passwordComfirm && this.password.length >= 6
    }
  }
}
</script>

