<script>

import {fnSignformatter,
fnHideOnClickOutside,
fnRegreArray,
fnPegaDataUTCNum,
//fnTextoEntreTags,
//fnFmtData,
//fnCampoValidoParaExibicao,
//fnDescItem,
//fnPegaStatus,
//fnPegaStatusChecagem,
//fnQueryInstIdData,
//fnConfigPeriodicidade
} from '../fn'
import SI from 'si-tools'

import {Highcharts, highchartsColors} from '../highchartsOptions'
import moment from '../momentLocale'
import regressao from '../regressao'

export default {
  props: ['store'],
  watch: {
    'winSize.w': function() {
      this.winSizeChangeHandler()
    },
    'graficoVisivelMobile': function(val) {
      this.$nextTick(() => {
        if (val === 'faixa') {
          this.chart.reflow()
        } else {
          this.chartHist.reflow()
        }
      })
    },
    'histPontoSel': function() {
      //this.atualizarGraficoHistorico()
    },
    'histSerieSel': function() {
      //this.atualizarGraficoHistorico()
    },
    'mudancaDeHist': function() {
      this.atualizarGraficoHistorico()
    },
    'eventoSelecionado': function() {
      if (this.eventoSelecionado !== 'Agora') {
        var horaEvento = moment(this.eventoSelecionado).format('DD-MM-YYYY-HH-mm-ss')
        this.$router.push({ query: { ...this.$route.query, evento: horaEvento }}).catch(err => {})
      } else {
        this.$router.push({query: {faixa: this.$route.query.faixa}}).catch(err => {})
      }
      this.executaAnalise({analiseDoUsuario: false})
    },
    'faixas': function () {
      this.selecionaFaixaPadrao()
    },
    'instrumentState.data.analise': {
      handler: function (newVal, oldVal) {
        this.executaAnalise({analiseDoUsuario: false})
      },
      deep: true
    },
    'respFreq': function() {this.atualizarGrafico()},
    'respFreqLogX': function() {this.atualizarGrafico()},
    analiseFaixa: {
      handler: function (newVal, oldVal) {
        if (this.analiseFaixa.tolVal === '' || this.analiseFaixa.tolUnidade === '') {
          return
        }
        this.executaAnalise({analiseDoUsuario: true})
      },
      deep: true
    },
    'faixaSelecionada': function () {
      this.histSerieSel = this.seriesFaixa[0].nomeSerie
      this.histPontoSel = this.seriesFaixa[0].data.Valor[0]

      this.$router.push({query: { ...this.$route.query, faixa: this.faixaSelecionada}}).catch(err => {})
      //this.atualizarGraficoHistorico()
      this.executaAnalise({analiseDoUsuario: false})
    },
    'exibirLegendas': function() {
      this.chart.legend.update({
        enabled: this.exibirLegendas
      })
    }
  },
  beforeRouteUpdate (to, from, next) {
    if (!to.query.faixa) return
    if (to.query.faixa !== from.query.faixa) {
      this.faixaSelecionada = ''+to.query.faixa
      var temEventoSel = !!(this.eventosDaFaixa.filter((item) => item.data === this.eventoSelecionado)).length
      if (!temEventoSel) {
        this.eventoSelecionado = 'Agora'
      }
    }
    next()
  },
  data () {

    return {
      instrumentState: this.store || INSTRUMENT_STORE.state,
      analiseFaixa: {},
      faixaSelecionada: null,
      chart: null,
      chartHist: null,
      exibirLegendas: false,
      respFreq: false,
      respFreqLogX: false,
      seriesColors: highchartsColors,
      dadosFaixa: null, 
      resultadoAnalises: null,
      eventoSelecionado: null,
      histSerieSel: null,
      histPontoSel: null,
      toleranciaCalculada: null,
      valorDeTeste: null,
      graficoVisivelMobile: 'faixa',  //'tendencia'
      winSize: WIN_SIZE,
      ehBulmaTablet: undefined,
      
      exibirFormulas: false,
      analiseNotas: null,
      formatoFormula: 'excel', // al
      nomeCelula: 'A1',
      configUpdateTodasAsFaixas: false,
      novaConfObservacao: ''
    };
  },
  created () {
  },
  mounted () {
    this.eventoSelecionado = 'Agora'
    
    
    this.selecionaFaixaPadrao()
    this.criarGrafico()
    this.criarGraficoHist()
    this.winSizeChangeHandler()
  },
  methods: {
    gravaConfHandler () {
      var obj = {
        analise: {}
      }
      var timestamp = Date.now()
      var analiseFaixa = _.cloneDeep(this.analiseFaixa)

      
      if (analiseFaixa.lhs !== 'tol') {
        delete analiseFaixa.tolUnidade
        delete analiseFaixa.tolVal
        delete analiseFaixa.tolVal
        delete analiseFaixa.tolValNaUnidade
      }

      if (this.configUpdateTodasAsFaixas) {
        obj.analise.global = analiseFaixa
        obj.analise.global.timestamp = timestamp
      } else {
        obj.analise[this.$route.query.faixa] = analiseFaixa
        obj.analise[this.$route.query.faixa].timestamp = timestamp
      }      
      
      obj['_id'] = ''+this.instrumentState.data.inst._id+'_config_'+timestamp
      obj['autor'] = this.instrumentState.userCtx['name']
      obj['timestamp'] = timestamp
      obj['observacao'] = !!this.novaConfObservacao.length ? this.novaConfObservacao : null
      obj['type'] = 'config_'+this.instrumentState.data.inst._id
      
      this.instrumentState.db.put(obj)
      .then( (response) => {
        this.instrumentState.modals.confirmaGravaConfFormVisivel = false
        this.novaConfObservacao = ''
        this.$toasted.show('Configuração gravada com sucesso.', {icon: 'fa-check-circle'})
      })
      .catch( (err) => {
        this.$toasted.show('Erro ao gravar configuração.', {icon: 'fa-exclamation-triangle'})
      })
      .finally( () => {

      })
    },
    /**
    
    metodos para resultados do ponto de teste

    */
    formulaDropDown (e) {
      var tgt = e.target.closest('.dropdown')
      tgt.classList.add('is-active')
      fnHideOnClickOutside(tgt)

    },
    pegaTextoRegressaoParaTipo (tipoReg, nomeSerie) {
      const serie = _.find(this.equacaoResultadoAnalises, {nomeSerie: nomeSerie})
      var texto = serie[tipoReg].termosRegressao
      if (this.formatoFormula === 'al') {
        
        let prefVal = SI.parse('1'+this.prefixo).number
        
        //let valPrefixoFmt = ((prefVal === 1) || !prefVal) ? null : '*'+prefVal.toExponential()

        var res = texto.formula.replace(/\n/g,'').replace(/ /g,'').replace(/se/g,'if').replace(/x/g,'leitura')

        res = res.replace(/[0-9]*\.?\,?[0-9]+([eE][-+]?[0-9]+)?/g, (match, contents, offset, input_string) => {
           return (parseFloat(match.replace(',','.'))*prefVal).toExponential(2)
        })

        //console.log(res)
        return res
        
      }
      return '='+texto.formula.replace(/x/g, this.nomeCelula.length ? this.nomeCelula : 'A1')
    },

    /**
    
    metodos para grafico de historico

    */
    winSizeChangeHandler () {
      if (this.winSize.w <= 768) {
        this.ehBulmaTablet = true
      } else {
        this.ehBulmaTablet = false
      }
    },
