    /**
    
    metodos para grafico da faixa

    */
    ajustarArea (res) {
      if (res.data.length === 1) {
        res.data.push([
          res.data[0][0],
          res.data[0][1],
          res.data[0][2]
        ])
        res.data[0][0] = res.data[0][0] * 0.99
        res.data[1][0] = res.data[1][0] * 1.01
      }
    },
    criarGrafico () {
      var el = this.$refs['grafico-faixa-container']
      /* 
      Curva da faixa
      */
      var prefixoUnidade = this.prefixoUnidade
      this.chart = Highcharts.chart(el, {
        exporting: {
            enabled: false
        },
        title: {
          text: this.titulo
        },
        subtitle: {
          text: ' '
        },        
        chart: {
          zoomType: 'xy'
        },
        credits: {
          enabled: false
        },
        legend: {
          enabled: this.exibirLegendas || false,
          itemMarginTop: 3,
          itemMarginBottom: 3,
          backgroundColor: 'rgba(0, 0, 0, 0.2)',
          layout: 'horizontal',
          floating: false,
          align: 'right',
          verticalAlign: 'bottom',
          x: 0,
          y: 0,
          shadow: false,
          navigation: {
            activeColor: '#3E576F',
            animation: true,
            arrowSize: 12,
            inactiveColor: '#CCC',
            style: {
                fontWeight: 'bold',
                color: '#333',
                fontSize: '12px'
            }
          }

        },
        tooltip: {
          enabled: false
          /*pointFormatter () { return pointformatter(this, prefixoUnidade) },
          headerFormat: '<span style="font-size: 10px">{point.key} '+prefixoUnidade+'</span><br/>'*/
        }
      })


    },
    removerSeries () {
      for (var i = this.chart.series.length - 1; i >= 0; i--) {
        this.chart.series[i].remove(false)
      }
    },
    atualizarGrafico () {
      
      if (this.chart.title) {
        this.chart.title.update({
          text: this.titulo
        })
      }

      if (this.chart.subtitle) {
        this.chart.subtitle.update({
          text: this.ultimoCertFxSel.certificado + ' de ' + this.dataFmt
        })
      }

      this.chart.yAxis[0].setTitle({
        text: 'Erro ' + this.prefixoUnidadeParentesesY
      });

      
      this.chart.xAxis[0].setTitle({
        text: 'Ponto ' + this.prefixoUnidadeParentesesX
      });

      var nSeriesNoGrafico = this.chart.series.length
      var series = this.flattenSeries
      
      // Faz com que o grafico nao seja animado enquanto
      // as series são adicionadas.
      // Isto evita que o grafico nao anime nunca
      // quando tem varias series e aumenta a performance,
      // pois a animaçao nao é executada diversas vezes
      const animarGrafico = false
      if (series.length !== nSeriesNoGrafico) {
      
        this.removerSeries()
        for (var i = 0; i < series.length; i++) {
          this.chart.addSeries(series[i], animarGrafico)
        }
      
      } else {

        for (var i = 0; i < series.length; i++) {
          
          // Atualiza nome e label da serie
          if(series[i].name !== this.chart.series[i].name) {
            this.chart.series[i].update({
              name: series[i].name
            })
          }
          
          this.chart.series[i].setData(
            series[i].data, animarGrafico
          )
        }

      }

      this.chart.xAxis[0].update({
        min: null,
        max: null,
        type: (this.respFreqLogX && this.podeTerRespFreq && this.respFreq) ? 'logarithmic' : null
      })
      this.chart.redraw()
    },

    /**
    
    outros metodos

    */

    executaAnalise (arg) {
      var ana
      if (arg.analiseDoUsuario) {
        ana = new this.instrumentState.Analises(this.instrumentStateDataEventoSel, this.faixaSelecionada, this.analiseFaixa)
      } else {
        ana = new this.instrumentState.Analises(this.instrumentStateDataEventoSel, this.faixaSelecionada)
        this.analiseFaixa = _.clone(ana.analiseFaixa)
        // retorna aqui, pois ao definir `analiseFaixa`, esta função será chamada novamente
        // com o argumento arg.analiseDoUsuario verdadeiro
        return
      }
      ana.executa()
      this.dadosFaixa = ana.dadosFaixa
      this.resultadoAnalises = ana.resultadoAnalises
      this.toleranciaCalculada = ana.toleranciaCalculada
      this.analiseNotas = ana.analiseNotas
      this.atualizarGrafico()
      this.atualizarGraficoHistorico()
    },
    selecionaFaixaPadrao () {
      if (this.instrumentState.faixas.length) {
        var faixaQuery = ''+this.$route.query.faixa

        var temDadosFaixaQuery = this.instrumentState
          .faixas
          .filter((f) => f.CodigoFaixaModelo === faixaQuery)
          .length
        ;

        if (faixaQuery && temDadosFaixaQuery) {
          this.faixaSelecionada = faixaQuery
        } else {
          this.faixaSelecionada = this.instrumentState.faixas[0].CodigoFaixaModelo
        }
      }
    }
  },
  computed: {
    ocultarConformidade () { // TODO: Verificar
      return false
    },
    textoAnalise () {
      return this.ehNovaExatidao ? 'Nova Incerteza' : 'EMA'
    },
    colunas () {

      var res = [
        {
          ////w: this.modoCompacto ? null : '100',
          //w: '100'
          prop: 'ValorDoInstrumento',
          label: 'Ponto ' + this.prefixoUnidadeParentesesX
        },
        {
          //w: '100'
          prop: 'ResultadoMedicao',
          label: 'Erro ' + this.prefixoUnidadeParentesesY
        },
        {
          //w: '100'
          prop: 'IncertExpandidaUsada',
          label: 'U ' + this.prefixoUnidadeParentesesY
        },
        {
          //w: '100'
          prop: 'tol',// se fir `analise`, pega area verde, `tol` pega a area vermelha
          label: this.textoAnalise + ' ' + this.prefixoUnidadeParentesesY
        }
      ]

      if (!this.ocultarConformidade) {
        res = res.concat({
          //w: '130'
          prop: 'pontoConforme',
          label: 'Conformidade'
        })
      }

      /*const nSeries = _.unionBy(
        this.resultadoAnalisesFlattenDeep, 'nomeSerie')
        .length
      ;*/

      if (this.nSeries > 1) {
        res = ([{
          ////w: this.modoCompacto ? null : '80'
          //w: '80'
          prop: 'nomeSerie',
          label: 'Série'
        }].concat(res))
      }

      return res  
    },
    dadosTabela () {
      const nomesColunas = this.colunas.map((col) => col.prop)

      var res = this.resultadoAnalisesFlattenDeep.map((res) => {
        var obj = {}
        nomesColunas.map((nome) => {
          const value = res[nome]
          if (_.isNumber(value)) {
            obj[nome] = fnSignformatter(value)
          }
          // Analises onde é retornado os limites superior 
          // e inferior em de um array
          if (_.isArray(value)) {
            obj[nome] = (fnSignformatter(Math.abs(value[0])))
          }
          if (_.isString(value)) {
            obj[nome] = value
          }
          if (_.isBoolean(value)) {
            obj[nome] = value ? true : false
          }
        })
        return obj
      })
      return res
    },
    prefixoUnidadeRes () {
      if (this.formatoFormula === 'al') {
        return this.dadosFaixa.Unidade
      }
      return this.prefixoUnidade
    },
    textoValorCorrigido () {
      return this.analiseFaixa.aplicarCorrecao ? 'valor corrigido: ' : ''
    },
    textoInc () {
      return this.resultadosPontoTeste.map((res) => {
        return this.pegaTextoRegressaoParaTipo('regInc', res[1])
      })
    },
    textoCorr () {
      return this.resultadosPontoTeste.map((res) => {
        return this.pegaTextoRegressaoParaTipo('regCorr', res[1])
      })
    },
    tamCorr () {
      return this.ehNovaExatidao ? 12 : 24
    },
    resultado2fmt () {
      return this.resultadosPontoTeste.map((res) => {
        if (this.ehNovaExatidao) {
          return '(' + res[2].replace(/\./g, ',') + ')'
        } else {
          return res[2].split(' ')[0].replace(/\./g, ',')
        }

      })
    },
    ehNovaExatidao () {
      return this.analiseFaixa.lhs !== 'ema' && this.analiseFaixa.lhs !== 'tol'
    },
    /**
    
    Regressao do ponto

    */
    equacaoResultadoAnalises () {
      if (!this.resultadoAnalises || !this.toleranciaCalculada) return
      var tol = this.toleranciaCalculada
      return this.resultadoAnalises.map((s, s_i) => {
        var obj = {}
        obj.CodigoFaixaModelo = s[0].CodigoFaixaModelo
        obj.nomeSerie = s[0].nomeSerie
        const dataInc = s.map((p, p_i) => [p.ValorDoInstrumento, tol[s_i][p_i][1]])
        const regInc = regressao(dataInc)

        const dataCorr = s.map((p) => [p.ValorDoInstrumento, -p.ResultadoMedicao])
        const regCorr = regressao(dataCorr)
        
        const res = _.extend(obj, {regInc: regInc, regCorr: regCorr} )
        return res
        ;
      })
    },
    resultadosPontoTeste () {
      if (!this.equacaoResultadoAnalises) return
      var res = []
      this.equacaoResultadoAnalises.map((s, si) => {
        var valorDeTeste = parseFloat(this.valorDeTeste)
        
        const arrInc = s['regInc'].equacaoArr
        const arrCorr = s['regCorr'].equacaoArr

        var regreInc, regreCorr
        regreInc = fnRegreArray(arrInc, valorDeTeste)
        regreCorr = fnRegreArray(arrCorr, valorDeTeste)

        // computa valor
        let inc = regreInc[0] * valorDeTeste + regreInc[1]
        
        var val
        if ((typeof valorDeTeste !== 'number') || isNaN(valorDeTeste)) {
          val = 0.0
        } else {
          if (this.analiseFaixa.aplicarCorrecao) {
            val = (valorDeTeste +
              regreCorr[0] *
              valorDeTeste +
              regreCorr[1])
              .toString()
            ;
          } else {
            val = this.valorDeTeste
          }
        }
        
        if (inc !== Infinity) {
          var incFmt
          if (inc <= 0) {
            incFmt = '0'
          } else {
            incFmt = fnSignformatter(inc)
          }

          let incFmtSplit = incFmt.split('.')

          // numero casas decimais do valor
          var nDecimalVal
          if (incFmtSplit.length === 2) {
            nDecimalVal = incFmtSplit[1].length
          } else {
            nDecimalVal = 0
          }
          
          res.push([
            this.seriesColors[si],
            s.nomeSerie,
            (parseFloat(val)).toFixed(nDecimalVal) +
            ' ± ' +
            incFmt]
            )
          ;
        } else {
          res.push([this.seriesColors[si], s.nomeSerie, 'Infinito'])
        }
      })
      return res
    },



    pontosDaSerieSelecionada () {
      return this.pontosExistenteParaCadaSerie[this.histSerieSel]
    },
    ehPrimeiroPontoDoHistorico () {
      return this.nomesDasSeries[0] === this.histSerieSel &&
        parseFloat(this.histPontoSel) === this.pontosDaSerieSelecionada[0]
      ;
    },
    ehUltimoPontoDoHistorico () {
      return this.nomesDasSeries[this.nomesDasSeries.length-1] === this.histSerieSel &&
        parseFloat(this.histPontoSel) === this.pontosDaSerieSelecionada[this.pontosDaSerieSelecionada.length - 1]
      ;
    },    
    mudancaDeHist () {
      return ''+this.histSerieSel+'___'+this.histPontoSel+'___'+this.faixaSelecionada
    },
    seriesFaixaAgrupadas () {
      return _.groupBy(this.seriesFaixa, 'nomeSerie')
    },
    nSeries () {
      return this.nomesDasSeries.length
    },
    nomesDasSeries () {
      return Object.keys(this.seriesFaixaAgrupadas)
    },
    pontosExistenteParaCadaSerie () {
      var res = {}
      this.nomesDasSeries.map((nomeSerie) => {
        var poltosVal = []
        this.seriesFaixaAgrupadas[nomeSerie].map((item) => {
          poltosVal = poltosVal.concat(item.data.Valor)
        })
        res[nomeSerie] = _.uniq(poltosVal)
      })
      return res
    },
    histSerieSelList () {
      if (!this.seriesFaixaAgrupadas) return
      if (!this.histSerieSel) return
      // Pode acontecer, na mudanca da faixa, de não existir um grupo, 
      // pois `histSerieSel` é da faixa anterior
      if (!this.seriesFaixaAgrupadas[this.histSerieSel]) return 
      var histList = this.seriesFaixaAgrupadas[this.histSerieSel].map((grupo) => {
        return grupo.historicosPontos
      })
      var histListsFlat = _.flattenDeep(histList)
      return histListsFlat
    },
    histPontoSelList () {
      if (!this.histSerieSelList) return
      var lista = this.histSerieSelList.filter((item) => item.serie.Valor === parseFloat(this.histPontoSel))
      //console.log(typeof this.histPontoSel)
      var res = _.uniqBy(lista, 'HoraInicial')
      return res
    },
    serieHist () {
      var serie = {
        name: 'Tendência',
        id: 'Historico',
        yAxis: 0,
        type: 'line'
      }
      serie.data = this.historicoDoPontoNaoNulo.map( (data) => {
        return [
          fnPegaDataUTCNum(data.HoraInicial),
          data.serie.ResultadoMedicao
        ]
      })

      return serie
    },
    serieIncertezaHist () {

      var serie = {
        whiskerLength: 10,
        name: 'Incerteza',
        yAxis: 0,
        //linkedTo: 'Historico',
        type: 'errorbar'
      }
      serie.data = this.historicoDoPontoNaoNulo.map( (data) => {
        const U = data.serie.IncertExpandidaUsada
        const ResultadoMedicao = data.serie.ResultadoMedicao

        var IncertezaInferior
        var IncertezaSuperior

        if (ResultadoMedicao !== null) {
          IncertezaInferior = ResultadoMedicao - U
          IncertezaSuperior = ResultadoMedicao + U
        } else {
          IncertezaInferior = null
          IncertezaSuperior = null
        }

        return [
          fnPegaDataUTCNum(data.HoraInicial),
          IncertezaInferior,
          IncertezaSuperior
        ]
      })

      return serie

    },
    resultadoAnalisesFlattenDeep () {
      return _.flattenDeep(this.resultadoAnalises)
    },
    resPontoSel () {
      return this.resultadoAnalisesFlattenDeep.filter((item) => {
        return item.Valor === parseFloat(this.histPontoSel) &&
          item.nomeSerie === this.histSerieSel
      })[0]
    },
    serieTolHist () {
      var ErroMinimoFaixa, ErroMaximoFaixa
      if (this.resPontoSel) {
        const tol = this.resPontoSel.tol
        ErroMinimoFaixa = tol[0]
        ErroMaximoFaixa = tol[1]        
      }
      
      var serie = {
        whiskerLength: 10,
        name: 'EMA',
        yAxis: 0,
        type: 'arearange',
        marker: {enabled: false},
        fillOpacity: 0,
        //linkedTo: 'Historico',
        dashStyle: 'Dash',
        lineColor: 'red',
        color: '#ff0000'
      }
      serie.data = this.historicoDoPontoNaoNulo.map( (data) => {
        if (!this.resPontoSel) {
          ErroMinimoFaixa = data.serie.ErroMinimoFaixa
          ErroMaximoFaixa = data.serie.ErroMaximoFaixa
        }
        const U = data.serie.IncertExpandidaUsada

        var IncertezaInferior
        var IncertezaSuperior

        if ((ErroMinimoFaixa !== null) && (ErroMinimoFaixa !== 0)) {
          IncertezaInferior = ErroMinimoFaixa
          IncertezaSuperior = ErroMaximoFaixa
        } else {
          IncertezaInferior = null
          IncertezaSuperior = null
        }

        return [
          fnPegaDataUTCNum(data.HoraInicial),
          IncertezaInferior,
          IncertezaSuperior
        ]
      })

      this.ajustarArea(serie)
      return serie

    },
    tituloHist () {
      if (!this.pontoSel) return ''
      
      const valPonto = ''+this.pontoSel.val
      const nomeSerie = this.pontoSel.nomeSerie
      return valPonto.toString() + ' ' +
        this.prefixoUnidade + 
        ( nomeSerie !== 'Série' || nomeSerie !== '' ?
        ' (' + this.pontoSel.nomeSerie + ')' : '' )
      ;
    },
    historicoDoPontoNaoNulo () {
      return _.chain(this.histPontoSelList)
        .filter((x) => (x.serie.ResultadoMedicao !== null))
        .sortBy((x) => fnPegaDataUTCNum(x.HoraInicial))
        .value()
      ;
    },
    /*tickPositionsComp () {
      // Pega o item que representa a data
      const res = this.serieHist.data.map((item) => item[0])
      return res
    },*/
    instrumentStateDataEventoSel () {
      if (!this.$route.query.evento) return this.instrumentState.data
      var data = {}
      Object.keys(this.instrumentState.data).map((k) => {
        if (k === 'seriesDenormRes') {
          data[k] = this.seriesFaixaEvento
        } else if (k === 'config') {
          data[k] = this.configDaFaixaListEvento
        } else {
          data[k] = this.instrumentState.data[k]
        }
      })
      return data
    },
    eventoTimeStamp () {
      if (!this.$route.query.evento) return Infinity
      // multiplica por 1000 para converter em ms, e soma 1000 para compensar a perda de 
      // da informação dos milisegundos. Para não ser necessario, incluir a 
      // informação de milisegundos na url
      return (moment(this.$route.query.evento, 'DD-MM-YYYY-HH-mm-ss').unix())*1000+1000
    },
    configDaFaixaList () {
      return this.instrumentState
        .data
        .config
        .filter((item) => {return item.analise && (item.analise[this.faixaSelecionada] || item.analise.global)})
      ;
    },
    configDaFaixaListEvento () {
      return this.configDaFaixaList.filter((c) => c.timestamp <= this.eventoTimeStamp)
    },
    eventosDaFaixa () {
      var eventos = []
      var certificados = {}
      this.seriesFaixa.map((s) => {
        if (!certificados[s.Certificado]) {
          eventos.push({
            tipo: 'calibração',
            data: moment(s.HoraInicial).unix()*1000,
            dataHuman: moment(s.HoraInicial).format('DD/MM/YYYY')
          })
          certificados[s.Certificado] = true
        }
      })
      
      this.configDaFaixaList.map((item) => {
        eventos.push({
          tipo: 'análise',
          data: item.timestamp,
          dataHuman: moment(item.timestamp).format('DD/MM/YYYY')
        })
      })
      return _.reverse(_.sortBy(eventos, 'data'))
    },
    dataFmt () {
      const dataStr = this.ultimoCertFxSel.hora

      if (!dataStr) return ''

      return moment(dataStr)
        .format("DD/MM/YY")
      ;
    },
    ultimoCertFxSel () {
      if (!this.seriesFaixaEvento.length) return {certificado: null, hora: null}
      return {
        certificado: this.seriesFaixaEvento[0].Certificado,
        hora: this.seriesFaixaEvento[0].HoraInicial
      }
    },    
    prefixoUnidade () {
      if (!this.seriesFaixaEvento.length) return ''
      var sf = this.seriesFaixaEvento[0]
      return sf.Prefixo + sf.Unidade
    },
    prefixoUnidadeParenteses () {
      if (this.prefixoUnidade !== '') {
        return '('+this.prefixoUnidade+')'
      }
      return ''
    },
    prefixoUnidadeParentesesX () {
      if (this.respFreq) {
        return '(Hz)'
      }
      return this.prefixoUnidadeParenteses
    },
    prefixoUnidadeParentesesY () {
      return this.prefixoUnidadeParenteses
    },
    
    /**
    Propriedades computadas relacionadas exclusivamente ao gráfico de análise da faixa
    */

    analisesDisponiveis () {
      return Object.keys(this.instrumentState.funcoes).map((item) => {
        return {
          // Muda texto para Personalizado quando for tol, pois fica mais intuitivo para o usuario
          desc: item === 'tol' ? 'Personalizado' : this.instrumentState.funcoes[item].desc,
          nome: item
        }
      })
    },
    titulo () {
      if (!this.faixaSelecionada) return null
      if (this.seriesFaixaEvento.length) return this.seriesFaixaEvento[0].DescFaixa
    },
    podeTerRespFreq () {
      return this.seriesFaixaEvento
        .map((s) => SI.parse(s.nomeSerie))
        .reduce((acc, curr) => ((curr.number >= 0) && 
          //curr.prefix && 
          (_.trim(curr.unit) === 'Hz')) && acc, true
        )
      ;
    },
    seriesGrafico () {
      const series = this.dadosFaixaFmt.series.map( (serie, serie_i) => {
        var res = {}
        var resMedUsado
        res.name = serie.nome
        res.id = serie.nome
        res.data = []
        for (var i = 0; i < serie.data.ResultadoMedicao.length; i++) {
          resMedUsado = this.analiseFaixa.aplicarCorrecao ? 0 : serie.data.ResultadoMedicao[i]
          res.data.push([
            serie.data.ValorDoInstrumento[i],
            resMedUsado
          ])
        }
        res.yAxis = 0
        res.type = 'line'
        res.color = this.seriesColors[serie_i]
        return res
      })
      return series
    },
    seriesIncerteza () {
      const series =  this.dadosFaixaFmt.series.map( (serie) => {
        var res = Object({})
        var resMedUsado
        res.name = serie.nome + ' (Incerteza)'
        //res.linkedTo = serie.nome
        res.data = []
        for (var i = 0; i < serie.data.ResultadoMedicao.length; i++) {
          resMedUsado = this.analiseFaixa.aplicarCorrecao ? 0 : serie.data.ResultadoMedicao[i]
          res.data.push([
            serie.data.ValorDoInstrumento[i],
            resMedUsado - serie.data.IncertExpandidaUsada[i],
            resMedUsado + serie.data.IncertExpandidaUsada[i]
          ])
        }
        res.type = 'errorbar'
        res.whiskerLength = 10
        return res
      })
      return series
    },
    seriesTol () {
      const existeAnaliseDeTol = !!this.resSeriesFaixaSelFmt.length
      if(!existeAnaliseDeTol) return null
      const series =  this.dadosFaixaFmt.series.map( (serie, serie_i) => {

        var res = Object({})
        //res.linkedTo = serie.nome
        res.dashStyle = 'Dash'
        res.lineColor = 'red'
        res.name = serie.nome + ' (EMA)'
        res.id = res.name
        //res.lineWidth = 0
        res.data = this.resSeriesFaixaSelFmt[serie_i].map( (p) => 
          [p.ValorDoInstrumento].concat(p.tol[0] === null ? [undefined, undefined] : p.tol)
        )

        res.marker = {enabled: false}
        this.ajustarArea(res)
        res.color = '#ff0000'
        //res.color = seriesColors[serie_i]
        res.type = 'arearange'
        res.fillOpacity = 0
        return res
      })
      // TODO: Testar isto. Serve para unificar os emas?
      if (this.analiseAtiva === 'criterioAceitacao') {
        if (this.analiseFaixa.lhs !== 'ema') {
          let dadosLimiteUnificado = _.clone(series[0])

          dadosLimiteUnificado.data = _.chain(series)
            .map((s) => s.data)
            .flatten()
            .sortBy((p) => p[0])
            .value()
          ;

          //dadosLimiteUnificado.name = 'Limite'
          dadosLimiteUnificado.id = dadosLimiteUnificado.name
          return [dadosLimiteUnificado]
        }
      }
      
      return series
    },
    seriesResultadoAnalises () {
      const series =  this.dadosFaixaFmt.series.map( (serie, serie_i) => {

        var res = {}
        res.name = serie.nome + ' (Análise)'
        res.linkedTo = serie.nome
        res.data = []

        for (var i = 0; i < serie.data.ResultadoMedicao.length; i++) {
          res.data.push([
            serie.data.ValorDoInstrumento[i],
            this.resSeriesFaixaSelFmt[serie_i][i].analise[0],
            this.resSeriesFaixaSelFmt[serie_i][i].analise[1]
          ])
        }

        this.ajustarArea(res)
        
        res.marker = {enabled: false}
        res.type = 'arearange'
        res.dashStyle = 'Dot'
        res.color = this.seriesColors[serie_i]
        res.fillOpacity = 0.15
        return res
      })
      return series
    },
    flattenSeries () {
      var res = _.chain([this.seriesGrafico
          ,this.seriesResultadoAnalises
          ,this.seriesTol
          ,this.seriesIncerteza
        ])
        .flatten()
        .compact()
        .filter( (serie) => {
          const pontosNulosArr = serie.data.map((p) => p[1] === null)
          return !_.filter(pontosNulosArr, (p) => p).length
        })
        .sortBy((serie) => serie.name)
        .value()
      ;
      return res
    },
    faixas () {
      if (!this.instrumentState.faixas.length) {
        return null
      }
      return this.instrumentState.faixas
    },
    dadosFaixaFmt () {
      if (this.respFreq && this.podeTerRespFreq) {
        var df = []
        for (var i = 0; i < this.dadosFaixa.series.length; i++) {
          let serie = this.dadosFaixa.series[i]
          for (var j = 0; j < serie.data.Valor.length; j++) {
            let obj = Object.create({})
            _.keys(serie.data).map((key) => {
              obj[key] = serie.data[key][j]
            })
            obj.nome = serie.nome
            obj.cod = serie.cod
            obj.sortval = serie.sortval
            df.push(obj)
          }
        }
        
        var groupedByVal = _.groupBy(df, 'Valor')
        var respFreqSeries = _.keys(groupedByVal).map((val) => {
          var serie = groupedByVal[val]
          var serieData = {
            cod: 'respFreq_'+val,
            nome: val+' '+this.prefixoUnidade,
            sortval: null,
            data: {}
          }
          var keys = ['ErroMaximoFaixa',
            'ErroMinimoFaixa',
            'IncertExpandidaUsada',
            'ResultadoMedicao',
            'Valor',
            'ValorDoInstrumento',
            'ValorDoPadrao',
            'fatork'
          ]
          keys.map((key) => {
            serieData.data[key] = []
            for (var i = 0; i < serie.length; i++) {
              if (key === 'ValorDoInstrumento') {
                serieData.data[key].push(SI.parse(serie[i]['nome']).number)
              } else {
                serieData.data[key].push(serie[i][key])
              }
            }
          })
          return serieData
        })
        
        return {series: respFreqSeries}
      }
      return this.dadosFaixa
    },
    resSeriesFaixaSelFmt () {
      if (this.respFreq && this.podeTerRespFreq) {
        var respFreqFaixaSel = _.cloneDeep(this.resultadoAnalises)

        respFreqFaixaSel = _.flatten(respFreqFaixaSel).map((p) => {
          p.ValorDoInstrumento = SI.parse(p.nomeSerie).number
          p.cod = 'respFreq_'+p.Valor
          return p
        })
        let grouped = _.groupBy(respFreqFaixaSel, 'cod')
        
        // itera na mesma ordem das séries dos dados,
        // garantindo os resultados das analises correspondentes
        return this.dadosFaixaFmt.series.map((s) => grouped[s.cod])
      }
      return this.resultadoAnalises
    },
    series () {
      return this.instrumentState.data.seriesDenormRes
    },
    seriesFaixa () {
      return this.series.filter((item) => item.CodigoFaixaModelo === this.faixaSelecionada)
    },
    seriesFaixaEvento () {
      var res = this.seriesFaixa.filter((s) => moment(s.HoraInicial).unix()*1000 <= this.eventoTimeStamp)
      return res
    },
    analiseAtiva () {
      var res = this.instrumentState.funcoes[this.analiseFaixa.lhs].tipoAnalise
      return res
    }
  }
}
</script>

